import { Component, OnInit, Input, Output, EventEmitter, ChangeDetectionStrategy } from '@angular/core';
import { isEqual, isNil } from 'lodash';
import { BehaviorSubject } from 'rxjs';

@Component({
  selector: 'app-gallery-tags-single',
  templateUrl: './gallery-tags-single.component.html',
  styleUrls: ['./gallery-tags-single.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class GalleryTagsSingleComponent implements OnInit {
  @Input()
  label = '';

  @Input()
  public get tags(): string[] {
    return this._tags.value;
  }
  public set tags(value: string[]) {
    if (isEqual(value, this._tags.value)) {
      return;
    }

    this._tags.next(value);
  }
  private _tags = new BehaviorSubject<string[]>([]);

  @Input()
  public get selectedTag(): string {
    return this._selectedTag.value;
  }
  public set selectedTag(value: string) {
    if (isEqual(value, this._selectedTag.value) || isNil(value)) {
      return;
    }

    this._selectedTag.next(value);
  }
  private _selectedTag = new BehaviorSubject<string>('');
  @Output() selectedTagChange = new EventEmitter<string>();


  constructor() { }

  ngOnInit() {
  }

  handleSelectTag(tag: string) {
    this.selectedTagChange.emit(tag);
  }
}
