import { ChangeDetectorRef, OnDestroy } from '@angular/core';
import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { Select } from '@ngxs/store';
import { Observable, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { EngineState } from 'src/app/engine/state/engine.state';
import { Fullscreen } from 'src/app/shared/libraries/fullscreen/fullscreen';
import { ChapterReadyStateModel } from 'src/app/story/models/chapter-ready-state.model';
import { StoryChaptersState } from 'src/app/story/state/chapters/story.chapters.state';
import { HelpTypeEnum } from '../../models/help-type.enum';
import { ViewUI } from '../../models/view-ui.enum';
import { ViewUIService } from '../../view-ui.service';
import { ViewService } from '../../view.service';

@Component({
  selector: 'app-view-menu',
  templateUrl: './view-menu.component.html',
  styleUrls: ['./view-menu.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ViewMenuComponent implements OnInit, OnDestroy {
  viewUI = ViewUI;

  // ---------- Observables ----------
  @Select(StoryChaptersState.getSelectedChapterReadyState)
  selectedChapterReady$: Observable<ChapterReadyStateModel>;

  @Select(EngineState.getShowLabels)
  showLabels$: Observable<boolean>;


  // ---------- Class Variables ----------
  isFullscreen = false;
  canFullscreen = false;
  fullscreen$: Observable<Event>;
  ngUnsubscribe = new Subject();

  constructor(
    private viewUIService: ViewUIService,
    private viewService: ViewService,
    private changeDetectorRef: ChangeDetectorRef
  ) { }

  ngOnInit() {
    this.canFullscreen = Fullscreen.canFullscreen();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

  initFullscreenListener() {
    this.fullscreen$ = Fullscreen.onFullscreenChange();
    this.fullscreen$
      .pipe(
        takeUntil(this.ngUnsubscribe)
      )
      .subscribe(event => {
        this.isFullscreen = Fullscreen.isFullscreen();
        this.changeDetectorRef.markForCheck();
      });
  }

  handleToggleLabelsClick() {
    this.viewService.toggleLabels();
  }

  handleFullscreenClick() {
    Fullscreen.toggleFullscreen();
  }
  
  handleTutorialClick() {
    this.viewService.toggleTutorial(true);
  }

  handleHelpBasicsClick() {
    this.viewService.toggleHelp(true, HelpTypeEnum.Basics);
  }

  handleHelpShortcutsClick() {
    this.viewService.toggleHelp(true, HelpTypeEnum.Shortcuts);
  }

  handlePresentClick() {
    this.viewService.togglePresentation();
  }

  handleNewTabClick() {
    this.viewService.openInNewTab();
  }

  handleSearchClick() {
    this.viewService.toggleSearch();
  }

  handleDrawClick() {
    this.viewService.toggleWhiteboard(true);
  }

  toggleToolbar(bar: 'media' | 'tools' | 'toolbar') {
    this.viewService.toggleToolbar(bar);
  }

  isEnabled(layout: ViewUI) {
    return this.viewUIService.isUIEnabled(layout);
  }

  isVisible(layout: ViewUI) {
    return this.viewUIService.isVisible(layout);
  }
}
