import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnDestroy, OnInit, Renderer2 } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef } from '@angular/material';
import { Actions, ofActionDispatched, Select, Store } from '@ngxs/store';
import { Observable, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { UserProfileModel } from 'src/app/auth/models/user-profile.model';
import {
  DeleteUserProfile,
  DeleteUserProfileFailure,
  DeleteUserProfileSuccess,
  Logout,
  LogoutSuccess,
  LogoutFailure
} from 'src/app/auth/state/auth/auth.actions';
import { AuthState } from 'src/app/auth/state/auth/auth.state';
import { parseErrorMessage } from 'src/app/account/utils/account-error-utils';

const defaultErrorMessage = 'There was an error deleting your account.';

@Component({
  selector: 'app-delete-account-dialog',
  templateUrl: './delete-account-dialog.component.html',
  styleUrls: ['./delete-account-dialog.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class DeleteAccountDialogComponent implements OnInit, OnDestroy {

  @Select(AuthState.getUserProfile)
  userProfile$: Observable<UserProfileModel>;

  @Select(AuthState.getProfileDeleting)
  deleting$: Observable<boolean>;

  // state
  error = '';
  attempts = 0;

  form: FormGroup;

  private ngUnsubscribe = new Subject();

  constructor(
    private store: Store,
    private actions: Actions,
    private formBuilder: FormBuilder,
    private dialogRef: MatDialogRef<DeleteAccountDialogComponent>,
    private changeRef: ChangeDetectorRef
  ) { }

  ngOnInit() {
    this.initForm();
    this.initListeners();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();

    // cleanup UI
    this.setDialogTransparent(false);
  }

  initForm() {
    this.form = this.formBuilder.group({
      'confirm': [false, Validators.requiredTrue]
    });
  }

  initListeners() {
    // deleting
    this.deleting$
      .pipe(
        takeUntil(this.ngUnsubscribe)
      )
      .subscribe((deleting) => {
        // mark attempts
        if (deleting) {
          this.error = '';
          this.attempts++;
        }

        // set UI state
        this.setDialogTransparent(deleting);

        this.changeRef.markForCheck();
      });

    // success -> logout
    this.actions
      .pipe(
        ofActionDispatched(DeleteUserProfileSuccess),
        takeUntil(this.ngUnsubscribe)
      )
      .subscribe(() => {
        this.store.dispatch(new Logout());
      });

    // TODO: logging out always trigger 401 error code, i.e., LogoutFailure, investigate
    this.actions
      .pipe(
        ofActionDispatched(LogoutSuccess, LogoutFailure),
        takeUntil(this.ngUnsubscribe)
      )
      .subscribe(() => {
        this.redirectOnLogout();
        this.dialogRef.close();
      });

    // failure
    this.actions
      .pipe(
        ofActionDispatched(DeleteUserProfileFailure),
        takeUntil(this.ngUnsubscribe)
      )
      .subscribe((action: DeleteUserProfileFailure) => {
        this.error = parseErrorMessage(action.error) || defaultErrorMessage;
        this.changeRef.markForCheck();
      });
  }

  handleCancelClick() {
    this.dialogRef.close();
  }

  handleDeleteClick() {
    this.store.dispatch(new DeleteUserProfile());
  }

  redirectOnLogout() {
    // hard reload
    window.location.href = '/login';
  }

  private setDialogTransparent(transparent: boolean) {
    this.dialogRef.removePanelClass('transparent');
    if (transparent) {
      this.dialogRef.addPanelClass('transparent');
    }
  }

}
