import { Injectable } from '@angular/core';
import { Observable, from, of } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ClipboardService {  

  constructor() { }

  getClipboardAPISupport(): boolean {
    return 'clipboard' in navigator;
  }

  getClipboardAPI(): any {
    return navigator['clipboard'];
  }

  copyToClipboard(val: string): Observable<boolean> {
    if (this.getClipboardAPISupport()) {
      const clipboardAPI = this.getClipboardAPI();
      return new Observable<boolean>((observer) => {
        clipboardAPI.writeText(val)
          .then(() => {
            observer.next(true);
            observer.complete();
          })
          .catch((e) => {
            observer.next(false);
            observer.complete();
          });
      });
    } else { // async copy unsupported
      return of(false);
    }
  }

  copyInputFieldValue(input: HTMLInputElement | HTMLTextAreaElement): boolean {
    input.select();
    let copied = false;
    try {
      copied = document.execCommand('copy');
    } catch (err) {
      copied = false;
    }
    return copied;
  }
}
