import { ChapterModel } from '../../../story/models/chapter.model';
import { TimelineModel } from '../../../story/models/timeline.model';
import { uuid } from '../../../shared/uuid';
import { HotspotModel } from '../../../story/models/labels/hotspot.model';
import { BackgroundModel } from '../../../engine/models/background.model';
import { classToClass } from 'class-transformer';
import { omit, isEmpty, isEqual } from 'lodash';
import { LabelsService } from '../../../story/labels.service';

import { calculateLeafStateMap } from 'src/lib/graph/leaf-state';
import { calculateMiniminumSynchronization } from './minimum-synchronization';
import { GraphUtils } from 'src/lib/graph/graph-utils';
import { Store } from '@ngxs/store';
import { EngineNodesState } from 'src/app/engine/state/nodes/engine.nodes.state';
import { ColorRecipeModel } from '../../models/color-recipe.model';
