import { Component, OnInit, ChangeDetectionStrategy, Input, Output, EventEmitter, ChangeDetectorRef } from '@angular/core';
import { GalleryState } from '../../state/gallery.state';
import { Observable, combineLatest } from 'rxjs';
import { BookmarkModel } from 'src/app/bookmarks/models/bookmark.model';
import { Select, Store } from '@ngxs/store';
import { BulkActions, BulkActionEvent } from '../../models/bulk-action';
import { GalleryActions } from '../../state/gallery.actions';
import { GalleryBulkActions } from '../../state/gallery.state.model';
import { isNil } from 'lodash';
import { filter } from 'rxjs/operators';

@Component({
  selector: 'app-gallery-bulk-actions',
  templateUrl: './gallery-bulk-actions.component.html',
  styleUrls: ['./gallery-bulk-actions.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class GalleryBulkActionsComponent implements OnInit {
  @Select(GalleryState.getBulkActions)
  bulkActions$: Observable<GalleryBulkActions>;

  @Select(GalleryState.getFilteredResults)
  results$: Observable<BookmarkModel[]>;

  @Select(GalleryState.getCheckedResults)
  checkedResults$: Observable<string[]>;

  @Output()
  bulkAction = new EventEmitter<BulkActionEvent>();


  results: BookmarkModel[];
  checkedResults: string[];

  constructor(
    private store: Store,
    private changeDetectorRef: ChangeDetectorRef
  ) { }

  ngOnInit() {
    combineLatest(
      this.results$,
      this.checkedResults$
    ).pipe(
      filter(([results, checkedResults]) => {
        return !isNil(results) && !isNil(checkedResults);
      })
    ).subscribe(([results, checkedResults]) => {
      this.results = results;
      this.checkedResults = checkedResults;
      this.changeDetectorRef.markForCheck();
    });
  }

  handleSelectAllClick() {
    this.store.dispatch(new GalleryActions.CheckAll());
  }

  handleClearSelectionClick() {
    this.store.dispatch(new GalleryActions.SetCheckedItems([]));
  }

  handleActionClick(action: BulkActions) {
    this.bulkAction.emit({
      name: action,
      ids: this.store.selectSnapshot(GalleryState.getCheckedResults)
    });
  }
}
