import { Component, OnInit, ChangeDetectionStrategy, ChangeDetectorRef, Output, EventEmitter, OnDestroy } from '@angular/core';
import { ModulesService } from 'src/app/modules/modules.service';
import { compact, get } from 'lodash';
import { map, takeUntil } from 'rxjs/operators';
import { Select } from '@ngxs/store';
import { GalleryState } from '../../state/gallery.state';
import { Observable, Subject } from 'rxjs';
import { SearchConfigUnion } from '../../models/search-config';
import { SearchType } from '../../models/search-type.enum';

@Component({
  selector: 'app-gallery-clients',
  templateUrl: './gallery-clients.component.html',
  styleUrls: ['./gallery-clients.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class GalleryClientsComponent implements OnInit, OnDestroy {
  clients = [];
  selectedClient = '';
  ngUnsubscribe = new Subject();
  
  @Select(GalleryState.getSearchConfig)
  searchConfig$: Observable<SearchConfigUnion>;

  @Output()
  clientSelected = new EventEmitter<string>();

  constructor(
    private modulesService: ModulesService,
    private changeDetectorRef: ChangeDetectorRef
  ) { }

  ngOnInit() {
    this.initClients();
    this.observeClient();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

  initClients() {
    this.modulesService.getClients()
      .pipe(
        map(results => compact(results))  
      )
      .subscribe(results => {
        this.clients = results;
        this.changeDetectorRef.markForCheck();
      });
  }

  handleSelectClient(client: string) {
    this.clientSelected.emit(client);
  }

  observeClient() {
    this.searchConfig$
      .pipe(
        takeUntil(this.ngUnsubscribe)
      )
      .subscribe(config => {
        if (config.type === SearchType.Internal) {
          const parts = config.directory.split('/');
          this.selectedClient = '';

          if (parts[0] === 'client' && parts.length === 2) {
            this.selectedClient = parts[1];
          }

          this.changeDetectorRef.markForCheck();
        }
      });
  }

}
