import { Component, OnInit, ChangeDetectionStrategy } from '@angular/core';
import { Store, Select } from '@ngxs/store';
import { GalleryActions } from '../../state/gallery.actions';
import { SearchConfigUnion, PrivateSearchConfig, CategorySearchConfig, LoftSearchConfig } from '../../models/search-config';
import { GalleryState } from '../../state/gallery.state';
import { SearchType } from '../../models/search-type.enum';
import { Observable } from 'rxjs';
import { EnvironmentApplication } from 'src/environments/models/environment-application';

@Component({
  selector: 'app-gallery-source',
  templateUrl: './gallery-source.component.html',
  styleUrls: ['./gallery-source.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class GallerySourceComponent implements OnInit {
  searchType = SearchType;
  environmentApplication = EnvironmentApplication;

  @Select(GalleryState.getSearchType)
  searchType$: Observable<SearchType>;

  constructor(
    private store: Store
  ) { }

  ngOnInit() {
  }

  handleSelectSource(source: 'module' | 'dashboard' | 'loft') {
    let config: SearchConfigUnion;

    switch (source) {
      case 'module':
        config = new CategorySearchConfig('all');
        break;
      
      case 'dashboard':
        config = new PrivateSearchConfig('');
        break;

      case 'loft':
        config = new LoftSearchConfig('production', 'maleAdult');
        break;
    }

    this.store.dispatch(new GalleryActions.SetSearchConfig(config));
  }

}
