import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule, Injectable, ErrorHandler, Inject, InjectionToken } from '@angular/core';
import { AppWidgetComponent } from './app-widget.component';
import { AppWidgetRoutingModule } from './app-widget-routing.module';
import { NgxsModule } from '@ngxs/store';
import { LayoutModule } from '../../layout/layout.module';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { BookmarksModule } from '../../bookmarks/bookmarks.module';
import { EngineModule } from '../../engine/engine.module';
import { StoryModule } from '../../story/story.module';
import { OrganizationsModule } from '../../organizations/organizations.module';

import { LocaleConfigModule } from '../../locale-config.module';
import { DialogModule } from '../../dialogs/dialog.module';
import { ViewModule } from 'src/app/view/view.module';
import { ApiInterceptor } from 'src/app/interceptors/api.interceptor';
import { WidgetAuthModule } from 'src/app/widget-auth/widget-auth.module';
import { WidgetModule } from 'src/app/widget/widget.module';
import { NgxsReduxDevtoolsPluginModule } from '@ngxs/devtools-plugin';
import { environment } from 'src/environments/environment';
import { UrlSerializer } from '@angular/router';
import { CustomUrlSerializer } from 'src/app/shared/libraries/url/custom-url-serializer';
import { BreakpointModule } from 'src/app/breakpoint/breakpoint.module';
import { UndoModule } from 'src/app/undo/undo.module';
import { MarkdownConfigModule } from 'src/app/markdown-config.module';
import { RollbarErrorHandler, RollbarService, rollbarFactory } from 'src/app/rollbar';
import { EnvironmentPlatform } from 'src/environments/models/environment-platform';
import { NgxGoogleAnalyticsModule } from 'ngx-google-analytics';
import { APP_BASE_HREF, LocationStrategy, HashLocationStrategy } from '@angular/common';

declare const immer;
// enables es5 support, patches, and patchable Maps and Sets
immer.enableAllPlugins();
immer.enableES5();

// hash location strategy is needed to support offline use
const offlineProviders = [
  { provide: HTTP_INTERCEPTORS, useClass: ApiInterceptor, multi: true },
  { provide: UrlSerializer, useClass: CustomUrlSerializer },
  { provide: ErrorHandler, useClass: RollbarErrorHandler },
  { provide: RollbarService, useFactory: rollbarFactory },
  { provide: APP_BASE_HREF, useValue: './' },
  { provide: LocationStrategy, useClass: HashLocationStrategy },
];

const  onlineProviders = [  
    { provide: HTTP_INTERCEPTORS, useClass: ApiInterceptor, multi: true },
    { provide: UrlSerializer, useClass: CustomUrlSerializer },
    { provide: ErrorHandler, useClass: RollbarErrorHandler },
    { provide: RollbarService, useFactory: rollbarFactory },
  ];

@NgModule({
  declarations: [
    AppWidgetComponent
  ],

  imports: [
    // This has to be at the top due to a bug in NgxsOnInit (https://github.com/ngxs/store/issues/539)
    NgxsModule.forRoot(),

    BreakpointModule,
    
    // NgxsReduxDevtoolsPluginModule.forRoot({
    //   disabled: environment.production
    // }),
    // Locales
    LocaleConfigModule.forRoot(),

    // Google Analytics
    NgxGoogleAnalyticsModule.forRoot(environment.googleAnalyticsId),

    // Markdown Support
    MarkdownConfigModule,

    // NoopAnimationsModule, // Disable animations for faster UI
    BrowserModule,
    BrowserAnimationsModule,
    HttpClientModule,

    // App dependencies
    EngineModule,

    // Child features
    BookmarksModule,
    LayoutModule,
    OrganizationsModule,
    StoryModule,

    UndoModule,


    // Dialogs
    DialogModule,

    ViewModule,

    // Widget auth + analytics
    WidgetModule,
    WidgetAuthModule,

    // App routing
    AppWidgetRoutingModule
  ],
  providers: (environment.offline === true) ? offlineProviders : onlineProviders,
  bootstrap: [AppWidgetComponent]
})
export class AppWidgetModule { }
