import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  Input,
  OnDestroy,
  OnInit,
} from '@angular/core';
import { Store } from '@ngxs/store';
import { Subject } from 'rxjs';
import { debounceTime, takeUntil } from 'rxjs/operators';
import { LabelPinType } from 'src/app/story/models/labels/label-pin-type.enum';
import { LabelThemeEnum } from 'src/app/story/models/labels/label-theme.enum';
import { LabelModel } from 'src/app/story/models/labels/label.model';
import { StoryLabelsState } from 'src/app/story/state/labels/story.labels.state';

@Component({
  selector: 'app-label-pin',
  templateUrl: './label-pin.component.html',
  styleUrls: ['./label-pin.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class LabelPinComponent implements OnInit, OnDestroy {
  labelPinType = LabelPinType;
  labelThemeEnum = LabelThemeEnum;

  @Input()
  public get label(): LabelModel {
    return this._label;
  }
  public set label(value: LabelModel) {
    this._label = value;
  }
  private _label: LabelModel;

  @Input()
  labelTheme: LabelThemeEnum = LabelThemeEnum.StudioNext;

  @Input()
  selected = false;

  sequence: string | number;
  ngUnsubscribe = new Subject();

  constructor(
    private store: Store,
    private changeDetectorRef: ChangeDetectorRef
  ) {}

  ngOnInit() {
    this.initPinSequence();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

  initPinSequence() {
    this.store.select(StoryLabelsState.getChapterLabels)
      .pipe(
        takeUntil(this.ngUnsubscribe),
        debounceTime(100)
      )
      .subscribe((labels) => {
        // Wrapped in a timeout to ensure compatibility with form elements 
        this.sequence = this.store.selectSnapshot(StoryLabelsState.getLabelSequence(this.label));
        this.changeDetectorRef.markForCheck();
      });
  }
}
