import { ChangeDetectionStrategy, Component, OnDestroy, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatDialogRef, MatSnackBar } from '@angular/material';
import { Select, Store, Actions, ofActionDispatched } from '@ngxs/store';
import { Observable, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { GetUserProfile } from 'src/app/auth/state/auth/auth.actions';
import {
  CancelSubscription,
  CancelSubscriptionFailure,
  CancelSubscriptionSuccess
} from 'src/app/auth/state/subscription/subscription.actions';
import { SubscriptionState } from 'src/app/auth/state/subscription/subscription.state';
import { parseErrorMessage } from 'src/app/account/utils/account-error-utils';

const defaultErrorMessage = 'There was an error cancelling your subscription.';

@Component({
  selector: 'app-cancel-subscription-dialog',
  templateUrl: './cancel-subscription-dialog.component.html',
  styleUrls: ['./cancel-subscription-dialog.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class CancelSubscriptionDialogComponent implements OnInit, OnDestroy {

  @Select(SubscriptionState.getCancelling)
  cancelling$: Observable<boolean>;

  form: FormGroup;

  private ngUnsubscribe = new Subject();

  constructor(
    private store: Store,
    private actions: Actions,
    private formBuilder: FormBuilder,
    private dialogRef: MatDialogRef<CancelSubscriptionDialogComponent>,
    private snackbar: MatSnackBar
  ) { }

  ngOnInit() {
    this.initForm();
    this.initListeners();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

  initForm() {
    this.form = this.formBuilder.group({
      'confirm': [false, Validators.requiredTrue]
    });
  }

  initListeners() {
    // success 
    this.actions
      .pipe(
        ofActionDispatched(CancelSubscriptionSuccess),
        takeUntil(this.ngUnsubscribe)
      )
      .subscribe(() => {
        this.snackbar.open('Subscription successfully cancelled.');
        this.store.dispatch(new GetUserProfile(true));

        this.dialogRef.close();
      });

    // failure
    this.actions
      .pipe(
        ofActionDispatched(CancelSubscriptionFailure),
        takeUntil(this.ngUnsubscribe)
      )
      .subscribe((action: CancelSubscriptionFailure) => {
        const errorMessage = parseErrorMessage(action.error) || defaultErrorMessage;
        this.snackbar.open(errorMessage);
      });
  }

  handleCancelClick() {
    this.dialogRef.close();
  }

  handleCancelSubscriptionClick() {
    this.store.dispatch(new CancelSubscription());
  }

}
