  SetChapters,
  SelectChapter,
  NextChapter,
  PreviousChapter,
  LastChapter,
  FirstChapter,
  RemoveChapter, 
  ResetStoryChapters,
  RegisterChapterThumbnails,
  TakeChapterPicture,
  TakeChapterPictureSuccess,
  DuplicateChapter,
  CopyThumbnail,
  SetAllChaptersBackground,
  SetSelectedChapterBackground,
  ResetSelectedChapterBackground,
  SetChapterBackground,
  SelectChapterById,
  RecordCamera,
  SetChapterClippingPlane,
  ApplyChapterPatch,
  SetChapterCameraAction,
  SetChapterUI,
  SetChapterInfo,
  ReorderChapters,
  SetChapterReadyState,
  SetChapterPostProcessing,
  SetChapterTransitions,
  SetChapterTransparencyMode,
  SetChapterLegend,
  AddToChapterLegend,
  SetChapterCurrentLayers,
  SetChapterCurrentLayersIndexPosition,
