import { ChapterModel } from '../../models/chapter.model';
import { BackgroundModel } from 'src/app/engine/models/background.model';
import { appDefaultBackground } from 'src/app/dialogs/components/background-color-dialog/background-config';
import { ThumbnailConstants } from 'src/app/shared/thumbnail.constants';
import { ImageTypeEnum, ImageService } from 'src/app/shared/image.service';
import { SnapshotConfig } from 'src/app/engine/models/snapshot-config.model';
import { AnimationService } from 'src/app/engine/animation.service';
import { MediaModel } from '../../models/media.model';
import { ColorUtils } from 'src/app/shared/libraries/colors/colors';

import { EngineService } from 'src/app/engine/engine.service';
