import { Injectable } from '@angular/core';
import { TrimConfiguration } from './models/trim-config.model';
import { StoryModel } from '../story/models/story.model';
import { Store } from '@ngxs/store';
import { SerializeChapterService } from './serialize/chapter/serialize-chapter.service';
import { serializeGraph } from './serialize/scene-graph/serialize-graph';
import { cloneDeep, invert } from 'lodash';
import { ChapterModel } from '../story/models/chapter.model';
import { EngineService } from '../engine/engine.service';
import { BdGraphPropertyValueMap } from 'src/lib/graph/graph.model';
import { StoryAnimationModel } from '../story/models/story.model';
import { AnimationService } from '../engine/animation.service';
import { renamespace } from './serialize/renamespace/renamespace';
import { StoryLabelsState } from '../story/state/labels/story.labels.state';
import { EngineParticlesState } from '../engine/state/particles/particles.state';

declare const Human;

@Injectable({
  providedIn: 'root'
})
export class SerializeModuleService {

  constructor(
    private store: Store,
    private engineService: EngineService,
    private chapterSerializer: SerializeChapterService,
    private animationService: AnimationService
  ) {}

  private serializeUI(story: StoryModel) {

    const ui = { ...story.ui };
    if (story.chapters.length > 1) {
      ui.scrubber = false;
      ui.playPause = false;
      ui.tour = { chapters: true };
    }
    return ui;

  }

  // #NOTE: [EM] This shouldn't be necessary. The user should be able to explicitly state their intention.
  // programatically create the animation property based on the currently active chapter's timeline data
  private serializeAnimation(zeroethChapter: ChapterModel) {
    // is there data in the current chapter's timeline map?
    const zeroethChapterAnimated = this.animationService.isChapterAnimated(zeroethChapter);
  
    if (zeroethChapterAnimated) {
      return {
        startTime: zeroethChapter.timelineMap[0].start,
        loop: zeroethChapter.loop !== false, // default to true
        numChapters: 1
      };
    }
    
    return {};
  }

  private getClippedObjects(): Record<string, any> {
    const activeClips = Human.renderer.clip.getActiveClips();

    if (activeClips.length === 0) {
      return {};
    }

    return Human.scene.getLeaves().reduce((acc, objectId) => {
      const object = Human.scene.objects[objectId];
      if (object.culledByClip) {
        acc[objectId] = true;
      }
      return acc;
    }, {});
  }

  // creates a map of objects to trim from the scene graph
  private generateTrimObjectMap(chapters: ChapterModel[], trimConfig: TrimConfiguration): BdGraphPropertyValueMap {
    
    const sceneGraph = this.engineService.getSceneGraph(Human.scene.rootObjects);
    
    // use serialized chapters to query scene-graph for trimmable objects
    let shownObjectState = {};
    if (trimConfig.graph.hidden) {
      // #NOTE: [EM] This could be extended to include otehr 'shown' conditions e.g. clipped, opacity, etc.
      shownObjectState = {...shownObjectState, ...this.engineService.queryShownObjects(chapters, sceneGraph, true)};
    }

    // invert shown object state for trim object state
    const trimObjectState = {};
    // tslint:disable-next-line:forin
    for (const id in shownObjectState) {
      trimObjectState[id] = !shownObjectState[id];
    }

    const clippedObjects = trimConfig.graph.clipped 
      ? this.getClippedObjects() 
      : {};

    return {
      ...trimObjectState,
      ...clippedObjects
    };
  }

  // serialize the data in engine state for storage on BD server
  serialize(
    namespaceMap: {[key: string]: string}, 
    trimConfig: TrimConfiguration,
    replaceObjectId: boolean = false,
    sourceStory: StoryModel,
    sourceChapters: ChapterModel[],
    chapterIdMap
  ): {story: StoryModel} {
    const trimMap = this.generateTrimObjectMap(sourceChapters, trimConfig);

    // create a copy of the source story (the current story in state)
    const moduleData = cloneDeep(sourceStory);

    // replace storychapters with serialized chapters
    // I'm letting the chapter serializer keep renamespacing the min synchronization for now... it's too hairy to mess with right now.
    const serialized = this.chapterSerializer.serialize(
      sourceChapters,
      namespaceMap,
      this.store.selectSnapshot(StoryLabelsState.getLabels),
      chapterIdMap
    );
    
    moduleData.chapters = serialized;

    // replace story graph with serialized graph
    const rootObjects = Human.scene.rootObjects;
    moduleData.sceneGraph = serializeGraph(rootObjects, trimMap);

    // replace UI configuration for widget
    moduleData.ui = this.serializeUI(moduleData);

    // #NOTE: [EM] this is a hard overwrite, which will cause problems for BD content creators
    moduleData.animation = this.serializeAnimation(sourceChapters[0]) as StoryAnimationModel;
    
    moduleData.customData = {
      ...moduleData.customData,
      particleSystems: this.store.selectSnapshot(
        EngineParticlesState.getParticleSystemsList
      ),
    };

    moduleData.tweenParticleLookup = this.store.selectSnapshot(EngineParticlesState.getTweenParticleLookup);

    return {
      story: replaceObjectId 
        ? renamespace(moduleData, namespaceMap) 
        : moduleData
    };
  }
}
