import { Component, OnInit, ChangeDetectionStrategy, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { Select, Store } from '@ngxs/store';
import { GalleryState } from '../../state/gallery.state';
import { Observable, Subject } from 'rxjs';
import { SearchConfigUnion, CategorySearchConfig, CollectionSearchConfig, LoftSearchConfig } from '../../models/search-config';
import { SearchType } from '../../models/search-type.enum';
import { OrganizationsState } from 'src/app/organizations/state/organizations.state';
import { OrganizationModel } from 'src/app/organizations/models/organization.model';
import { CollectionModel } from 'src/app/organizations/models/collection.model';
import { takeUntil, filter, take, distinctUntilChanged } from 'rxjs/operators';
import { isNil, get, isEqual } from 'lodash';
import { GalleryResultsModel } from '../../models/gallery-results.model';
import { AuthState } from 'src/app/auth/state/auth/auth.state';
import { getCategory } from 'src/app/shared/features/site-navigation/site-navigation-utils';

@Component({
  selector: 'app-gallery-title',
  templateUrl: './gallery-title.component.html',
  styleUrls: ['./gallery-title.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class GalleryTitleComponent implements OnInit, OnDestroy {
  @Select(GalleryState.getSearchConfig)
  searchConfig$: Observable<SearchConfigUnion>;

  @Select(GalleryState.getSearchType)
  searchType$: Observable<SearchType>;

  @Select(OrganizationsState.getOrganization)
  organization$: Observable<OrganizationModel>;

  searchType = SearchType;
  ngUnsubscribe = new Subject();

  collection: string;
  categoryName: string;
  categoryId: string;
  directory: string;

  constructor(
    private store: Store,
    private changeDetectorRef: ChangeDetectorRef
  ) { }

  ngOnInit() {
    this.observeSearchType();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

  observeSearchType() {
    this.searchConfig$.pipe(
      distinctUntilChanged((a, b) => isEqual(a, b)),
      filter(config => !isNil(config)),
      takeUntil(this.ngUnsubscribe)
    ).subscribe(config => {
      switch (config.type) {
        case SearchType.Collection: 
          this.updateCollectionName(config);
          break;

        case SearchType.Category:
          this.updateCategoryName(config);
          this.categoryId = config.categoryId;
          break;

        case SearchType.Community:
          this.categoryId = config.categoryId;
          break;

        case SearchType.Loft:
          this.updateDirectoryName(config);
          break;
      }

      this.changeDetectorRef.markForCheck();
    });
  }

  updateCollectionName(config: CollectionSearchConfig) {
    // Uses a select subscription instead of selectSnapshot because the organization collections
    // may not have finished loading when the title component is rendered, possibly resulting
    // in an empty component on first page load.
    this.store.select(OrganizationsState.getCollections)
      .pipe(
        filter(Boolean),
        take(1),
        takeUntil(this.ngUnsubscribe)
      )
      .subscribe((collections: CollectionModel[]) => {
        const collection = collections.find(_collection => _collection.collection_id === config.collectionId);
        this.collection = get(collection, 'collection_name', 'Collection');
        this.changeDetectorRef.markForCheck();
      });

  }

  updateCategoryName(config: CategorySearchConfig) {
    if (config.categoryId == null) {
      this.categoryName = 'All Categories';
    } else {
      const language = this.store.selectSnapshot(AuthState.getUserLanguage);
      const category = getCategory(config.categoryId, language);
      this.categoryName = get(category, 'title', 'All Categories');
      this.changeDetectorRef.markForCheck();
    }
  }

  updateDirectoryName(config: LoftSearchConfig) {
    this.directory = [config.primary, config.secondary].join('/');
    this.changeDetectorRef.markForCheck();
  }
}
