import { Injectable } from '@angular/core';

import { Observable, combineLatest } from 'rxjs';
import { filter } from 'rxjs/operators';

import { Select, Store } from '@ngxs/store';

import { isNil } from 'lodash';

import { ScalarFresnelModel } from 'src/app/edit/models/scalar-fresnel.model';

import { StoryChaptersState } from 'src/app/story/state/chapters/story.chapters.state';

import { SetChapterCurrentLayersIndexPosition } from 'src/app/story/state/chapters/story.chapters.actions';
import { NodeActions } from 'src/app/engine/state/nodes/engine.nodes.actions';
import { EngineNodesState } from '../engine/state/nodes/engine.nodes.state';
import { VisibilityLayerModel } from './models/visibility-layers.model';

declare const Human: any;
declare const ScalarFresnel: any;

@Injectable({
  providedIn: 'root'
})
export class VisibilityLayersService {

  @Select(StoryChaptersState.getSelectedChapterCurrentLayers)
  selectedChapterCurrentLayers$: Observable<any>;

  @Select(StoryChaptersState.getSelectedChapterCurrentLayersIndex)
  selectedChapterCurrentLayersIndex$: Observable<any>;

  @Select(StoryChaptersState.getSelectedChapterCurrentLayersPosition)
  selectedChapterCurrentLayersPosition$: Observable<any>;

  @Select(StoryChaptersState.getSelectedChapterId)
  selectedChapterId$: Observable<any>;

  private currentLayers = [];

  private nodesLayerIndex = 0;
  private objectsLayerIndex = 0;

  private selectedChapterId: string;

  constructor(
    private store: Store
  ) {}

  initObservables () {
    this.selectedChapterId$.subscribe((selectedChapterId) => {
      this.selectedChapterId = selectedChapterId;
    });

    this.selectedChapterCurrentLayers$.subscribe((currentLayers) => {
      this.currentLayers = currentLayers;
      
      this.nodesLayerIndex = this.objectsLayerIndex = this.store.selectSnapshot(
        StoryChaptersState.getSelectedChapterCurrentLayersIndex
      );
    });

    combineLatest(
      this.selectedChapterCurrentLayersIndex$,
      this.selectedChapterCurrentLayersPosition$
    ).pipe(
      filter(([currentIndex, currentPosition]) => {
        return !isNil(currentIndex) && !isNil(currentPosition);
      })
    ).subscribe(([currentIndex, currentPosition]) => {
      this.synchronizeScene(currentIndex, currentPosition, true);
    });
  }

  // Update story chapter state and add to undo stack
  recordLayerIndexPosition (index: number, position: number) {
    this.store.dispatch(
      new SetChapterCurrentLayersIndexPosition(
        this.selectedChapterId, 
        index, 
        position,
        true
      )
    );
  }

  // Synch the scene either through the app's nodes model
  // Or directly via the engine.
  synchronizeScene(index: number, position: number, nodesMode: boolean) {
    this.updateLayerIndex(index, this.currentLayers, nodesMode);
    this.updateLayerPosition(position, this.currentLayers, nodesMode);
  }

  // Fraction calculation methods exclude the last layer
  fractionToIndex(fraction: number): number {
    const threshold = this.getThreshold();
    const index = Math.floor(fraction / threshold);
    return Math.min(this.currentLayers.length - 2, index);
  }

  fractionToPosition(fraction: number, index: number): number {
    const threshold = this.getThreshold();
    const position = (fraction - (threshold * index)) / threshold;
    return Math.max(0, Math.min(position, 1));
  }

  indexPositionToFraction (index: number, position: number): number {
    const threshold = this.getThreshold();
    return (index * threshold) + (position * threshold);
  }

  // Excludes the last layer
  private getThreshold () {
    return this.currentLayers.length > 1 ? 
      1 / (this.currentLayers.length - 1) : 1;
  }

  private getCurrentLayerIndexProp (nodesMode: boolean): string {
    return nodesMode ? 'nodesLayerIndex' : 'objectsLayerIndex';
  }

  private updateLayerIndex(newIndex: number, layersData: VisibilityLayerModel[], nodesMode: boolean) {
    const currentIndexProp = this.getCurrentLayerIndexProp(nodesMode);

    if (newIndex === this[currentIndexProp]) {
      return;
    }

    this.restoreLayers(newIndex, this[currentIndexProp], layersData, nodesMode);

    if (nodesMode) {
      this.nodesLayerIndex = newIndex;
    }

    this.objectsLayerIndex = newIndex; // Always synch objectsLayerIndex
  }

  private updateLayerPosition(newPosition: number, layersData: VisibilityLayerModel[], nodesMode: boolean) {
    const currentIndexProp = this.getCurrentLayerIndexProp(nodesMode);
    const objectIds = layersData[ this[currentIndexProp] ].objectIds;
    const opacity = 1 - newPosition;

    this.setObjectsOpacity(objectIds, opacity);
  }

  private restoreLayers(newIndex: number, oldIndex: number, layersData: VisibilityLayerModel[], nodesMode: boolean) {
    const diff = Math.abs(newIndex - oldIndex);
    const dir = (newIndex - oldIndex) / diff;
    let objectIds = [];

    let i = oldIndex;

    while (i !== newIndex) {
      objectIds = objectIds.concat(layersData[i].objectIds);
      i += dir;
    }
    
    // Update old / jumped layer(s)
    const opacity = dir === 1 ? 0 : 1;
    const shown = dir === -1;

    this.setObjectsOpacity(objectIds, opacity);

    if (nodesMode) {
      this.setNodesShown(objectIds, shown);
    } else {
      this.setObjectsShown(objectIds, shown);
    }
  
    // Always show current layer when moving right to left.
    if (dir === -1) {
      if (nodesMode) {
        this.setNodesShown(layersData[newIndex].objectIds, true);
      } else {
        this.setObjectsShown(layersData[newIndex].objectIds, true);
      }
    }
  }

  private setObjectsOpacity(objectIds: string[], opacity: number) {
    for (let i = 0; i < objectIds.length; i++) {
      let newOpacity;

      if (this.hasFresnelOpacity(objectIds[i])) {
        const scalarFresnelOpacity = 
          new ScalarFresnelModel(Human.scene.objects[ objectIds[i] ].opacity);
        scalarFresnelOpacity.power = opacity;
        newOpacity = ScalarFresnelModel.toEngine(scalarFresnelOpacity);
      } else {
        newOpacity = opacity;
      }

      Human.scene.objects[objectIds[i]].setOpacity(newOpacity);
    }
  }

  private hasFresnelOpacity(objectId: string): boolean {
    return Human.scene.objects[objectId].opacity instanceof ScalarFresnel;
  }

  private setObjectsShown(objectIds: string[], shown: boolean) {
    const update = {};

    for (let i = 0; i < objectIds.length; i++) {
      update[objectIds[i]] = shown;
    }

    Human.scene.setEnabledObjects({ objects: update });
  }

  private setNodesShown(objectIds, shown) {
    this.store.dispatch(
      new NodeActions.UpdateNodesShown(
        shown, 
        objectIds, 
        undefined, 
        false
      )
    );
  }

}
