import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';
import { ProfressionalSubscriptionModel } from './models/subscription/professional-subscription.model';
import { PurchaseSubscriptionModel } from './models/utils/subscription';
import { SubscriptionPlanModel } from './models/subscription/subscription-plan.model';

// TODO: should be stored/retrieved from the backend
const subscriptionPlans: SubscriptionPlanModel[] = [{
  id: 'monthly_web_mobile_3-99',
  plan_display_name: 'monthly premium individual',
  price: 3.99
}];


@Injectable({
  providedIn: 'root'
})
export class SubscriptionService {

  constructor(private http: HttpClient) { }

  getBraintreePlans(): Observable<SubscriptionPlanModel[]> {
    return of(subscriptionPlans);
  }

  getBraintreePlan(paymentplan: string, discountcode?: string): Observable<SubscriptionPlanModel> {
    return this.http.get(`/ws/professional/plan?paymentplan=${paymentplan}&discountcode=${discountcode}`)
      .pipe(
        map((response: any) => response.plan)
      );
  }

  getBraintreeClientToken(): Observable<string> {
    return this.http.get('/ws/professional/customer')
      .pipe(
        map((response: any) => response.client_token)
      );
  }

  getProfessionalSubscription(): Observable<ProfressionalSubscriptionModel> {
    return this.http.get('/ws/user/profile?subscription_hard_check=1')
      .pipe(
        map((response: any) => response.subscription as ProfressionalSubscriptionModel)
      );
  }

  buyProfessionalSubscription(request: PurchaseSubscriptionModel): Observable<any> {
    return this.http.post('/ws/professional/subscribe', {
      ...request,
      subscription_intent: 'buy'
    });
  }

  updateProfessionalSubscription(nonce: string): Observable<any> {
    return this.http.post('/ws/professional/subscribe', {
      payment_method_nonce: nonce,
      subscription_intent: 'update'
    });
  }

  cancelSubscription(): Observable<any> {
    return this.http.post('/ws/professional/cancel', {});
  }

}
