import { State, Action, StateContext, Selector } from '@ngxs/store';
import { OrganizationsStateModel } from './organizations.state.model';
import {
  SelectOrganization,
  SetOrganizations,
  SelectCollection,
  GetCollectionsSuccess,
  GetCollections,
  JoinOrganization,
  JoinOrganizationSuccess,
  JoinOrganizationFail,
  ResetOrganizations,
  JoinOrganizationWithCode,
  JoinOrganizationWithCodeFail,
  JoinOrganizationWithCodeSuccess,
  SendOrganizationInvite,
  SendOrganizationInviteSuccess,
  SendOrganizationInviteFail,
  GetOrganizationUsers,
  GetOrganizationUsersSuccess,
  GetOrganizationUsersFail
} from './organizations.actions';
import { OrganizationsService } from '../organizations.service';
import { WidgetOptions } from 'src/app/publish/models/widget-options.model';
import { PublishService } from 'src/app/publish/publish.service';
import { SetPublishWidgetURL } from 'src/app/publish/state/publish.actions';
import { finalize } from 'rxjs/operators';

const initialState = {
  joining: false,
  inviting: false,
  loadingUsers: false
};

@State<OrganizationsStateModel>({
  name: 'organizations',
  defaults: initialState
})
export class OrganizationsState {
  /**
   * Selectors
   */
  @Selector()
  static getOrganizations(state: OrganizationsStateModel) {
    return state.organizations;
  }

  @Selector()
  static getOrganization(state: OrganizationsStateModel) {
    return state.organization;
  }

  @Selector()
  static getSelectedOrganization(state: OrganizationsStateModel) {
    return state.selectedOrganization;
  }

  @Selector()
  static getCollections(state: OrganizationsStateModel) {
    return state.collections;
  }

  @Selector()
  static getSelectedCollection(state: OrganizationsStateModel) {
    return state.selectedCollection;
  }

  @Selector()
  static getWidgetURL(state: OrganizationsStateModel) {
    return state.widgetURL;
  }

  @Selector()
  static getWidgetOptions(state: OrganizationsStateModel) {
    return state.widgetOptions;
  }

  @Selector()
  static getAppFeatures(state: OrganizationsStateModel) {
    return state.appFeatures;
  }

  @Selector()
  static getJoining(state: OrganizationsStateModel) {
    return state.joining;
  }

  @Selector()
  static getInviting(state: OrganizationsStateModel) {
    return state.inviting;
  }

  @Selector()
  static getUsersLoading(state: OrganizationsStateModel) {
    return state.loadingUsers;
  }

  /**
   * Actions
   */
  @Action(SelectOrganization)
  selectOrganization(context: StateContext<OrganizationsStateModel>, action: SelectOrganization) {
    const organization = action.organization;
    context.patchState({
      selectedOrganization: organization,
      selectedCollection: null
    });

    // Update collections
    if (organization) {
      context.dispatch(new GetCollections(organization.id));
    }
  }

  @Action(SetOrganizations)
  setOrganizations(context: StateContext<OrganizationsStateModel>, action: SetOrganizations) {
    const organizations = action.organizations || [];
    const organization = organizations[0];

    let widgetURL: string;
    let widgetOptions: WidgetOptions = null;
    let appFeatures = { ... context.getState().appFeatures };

    // read organization widget settings
    if (organization && organization.group_attributes) {
      const attributes = organization.group_attributes;

     // set org default widget url
     const widgetURLAttribute = this.organizationsService.getOrganizationAttribute(attributes, 'widget_settings', 'widget_custom_url_path');
      if (widgetURLAttribute && widgetURLAttribute.value) {
        widgetURL = widgetURLAttribute.value;
      }

      // set org default options
      const widgetOptionsAttribute = this.organizationsService.getOrganizationAttribute(attributes, 'widget_settings', 'widget_url_params');
      if (widgetOptionsAttribute && widgetOptionsAttribute.value) {
        widgetOptions = this.publishService.extendWidgetOptions(new WidgetOptions(), widgetOptionsAttribute.value);
      }

      // set org app features
      const appFeaturesAttribute = this.organizationsService.getOrganizationAttribute(attributes, 'app_settings', 'web_app_features');
      if (appFeaturesAttribute) {
        appFeatures = {
          ...appFeatures,
          ...appFeaturesAttribute.value
        };
      }
    }

    context.patchState({
      organization: organization,
      organizations: organizations,
      widgetURL: widgetURL,
      widgetOptions: widgetOptions,
      appFeatures: appFeatures
    });

    // set default widget
    context.dispatch(new SetPublishWidgetURL(widgetURL));
  }

  @Action(JoinOrganization)
  joinOrganization(context: StateContext<OrganizationsStateModel>, { organization }: JoinOrganization) {
    context.patchState({ joining: true });

    this.organizationsService.addUserToOrganization(organization.eid)
      .pipe(finalize(() => context.patchState({ joining: false })))
      .subscribe(
        () => context.dispatch(new JoinOrganizationSuccess(organization)),
        (err) => context.dispatch(new JoinOrganizationFail(organization))
      );
  }

  @Action(JoinOrganizationWithCode)
  joinOrganizationWithCode(context: StateContext<OrganizationsStateModel>, { code }: JoinOrganizationWithCode) {
    context.patchState({ joining: true });

    this.organizationsService.addUserWithGroupCode(code)
      .pipe(finalize(() => context.patchState({ joining: false })))
      .subscribe(
        (organizationName) => context.dispatch(new JoinOrganizationWithCodeSuccess(organizationName)),
        (err) => context.dispatch(new JoinOrganizationWithCodeFail(err))
      );
  }

  @Action(SendOrganizationInvite)
  sendOrganizationInvite(context: StateContext<OrganizationsStateModel>, { groupEID, emails }: SendOrganizationInvite) {
    context.patchState({ inviting: true });

    this.organizationsService.sendOrganizationInvites(groupEID, emails)
      .pipe(finalize(() => context.patchState({ inviting: false })))
      .subscribe(
        (responses) => context.dispatch(new SendOrganizationInviteSuccess(responses)),
        (err) => context.dispatch(new SendOrganizationInviteFail(err))
      );
  }

  @Action(GetOrganizationUsers)
  getOrganizationUsers(context: StateContext<OrganizationsStateModel>, { groupEID }: GetOrganizationUsers) {
    context.patchState({ loadingUsers: true });

    this.organizationsService.getOrganizationUsers(groupEID)
    .pipe(finalize(() => context.patchState({ loadingUsers: false })))
      .subscribe(
        (users) => context.dispatch(new GetOrganizationUsersSuccess(users)),
        (err) => context.dispatch(new GetOrganizationUsersFail(err))
      );
  }

  @Action(GetCollections)
  getCollections(context: StateContext<OrganizationsStateModel>, action: GetCollections) {
    this.organizationsService.getCollections(action.organizationId)
      .subscribe((collections) => {
        context.dispatch(new GetCollectionsSuccess(collections));
      });
  }

  @Action(GetCollectionsSuccess)
  getCollectionsSuccess(context: StateContext<OrganizationsStateModel>, action: GetCollectionsSuccess) {
    context.patchState({
      collections: action.collections
    });
  }

  @Action(SelectCollection)
  selectCollection(context: StateContext<OrganizationsStateModel>, action: SelectCollection) {
    const { organization, selectedOrganization } = context.getState();
    const collection = action.collection;

    context.patchState({
      // ensure organization selected
      selectedOrganization: selectedOrganization || organization,
      selectedCollection: collection
    });
  }

  @Action(ResetOrganizations)
  resetOrganizations(context: StateContext<OrganizationsStateModel>, action: ResetOrganizations) {
    context.setState(initialState);
  }

  constructor(
    private organizationsService: OrganizationsService,
    private publishService: PublishService
  ) { }
}
