import { 
  Component,
  OnInit,
  ViewChild,
  ElementRef,
  Renderer2,
  AfterViewInit,
  OnDestroy,
  Output,
  EventEmitter,
  ChangeDetectionStrategy,
  ChangeDetectorRef } from '@angular/core';
import { Select, Store } from '@ngxs/store';
import { GalleryState } from '../../state/gallery.state';
import { Observable, Subject, fromEvent, merge } from 'rxjs';
import { GalleryViewMode } from '../../models/gallery-view-mode.enum';
import { BookmarkSortModel } from 'src/app/bookmarks/models/bookmark-sort.model';
import { filter, debounceTime, distinctUntilChanged, takeUntil } from 'rxjs/operators';
import { GalleryActions } from '../../state/gallery.actions';
import { SearchConfigUnion } from '../../models/search-config';
import { BookmarkModel } from 'src/app/bookmarks/models/bookmark.model';
import { BulkActionEvent } from '../../models/bulk-action';
import { GalleryBulkActions } from '../../state/gallery.state.model';
import { SearchType } from '../../models/search-type.enum';

@Component({
  selector: 'app-gallery-header',
  templateUrl: './gallery-header.component.html',
  styleUrls: ['./gallery-header.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class GalleryHeaderComponent implements OnInit, AfterViewInit, OnDestroy {
  @ViewChild('searchDesktop', { static: false }) searchDesktop: ElementRef;
  @ViewChild('searchMobile', { static: false }) searchMobile: ElementRef;
  
  @Output()
  bulkAction = new EventEmitter<BulkActionEvent>();

  @Select(GalleryState.getViewMode)
  viewMode$: Observable<GalleryViewMode>;

  @Select(GalleryState.getSort)
  sort$: Observable<BookmarkSortModel>;

  @Select(GalleryState.getSortOptions)
  sortOptions$: Observable<BookmarkSortModel[]>;

  @Select(GalleryState.getSearchConfig)
  searchConfig$: Observable<SearchConfigUnion>;

  @Select(GalleryState.getSearchType)
  searchType$: Observable<SearchType>;

  @Select(GalleryState.getFilteredResults)
  results$: Observable<BookmarkModel[]>;

  @Select(GalleryState.getBulkActions)
  bulkActions$: Observable<GalleryBulkActions>;

  @Select(GalleryState.getLoading)
  loading$: Observable<boolean>;

  galleryViewMode = GalleryViewMode;
  ngUnsubscribe = new Subject();

  constructor(
    private store: Store,
    private renderer: Renderer2,
    private changeDetectorRef: ChangeDetectorRef
  ) { }

  ngOnInit() {
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

  ngAfterViewInit() {
    this.observeSearchInput();
    this.observeSearchConfig();
  }

  observeSearchInput() {
    merge(
      fromEvent(this.searchDesktop.nativeElement, 'keyup'),
      fromEvent(this.searchMobile.nativeElement, 'keyup')
    )
    .pipe(
        filter(Boolean),
        debounceTime(600), // Slower than normal (300ms) because the results are so heavy
        distinctUntilChanged(),
        takeUntil(this.ngUnsubscribe)
    )
    .subscribe((event: KeyboardEvent) => {
      const input = <HTMLInputElement>event.target;
      const config = {...this.store.selectSnapshot(GalleryState.getSearchConfig)};
      config.query = input.value;

      this.store.dispatch(new GalleryActions.SetSearchConfig(config));
    });
  }

  observeSearchConfig() {
    this.searchConfig$
      .pipe(
        filter(config => {
          return config != null && 
            document.activeElement !== this.searchDesktop.nativeElement && 
            document.activeElement !== this.searchMobile.nativeElement;
        }),
        takeUntil(this.ngUnsubscribe)
      )
      .subscribe((config) => {
        this.renderer.setProperty(this.searchDesktop.nativeElement, 'value', config.query);
        this.renderer.setProperty(this.searchMobile.nativeElement, 'value', config.query);

        this.changeDetectorRef.markForCheck();
      });
  }

  handleSort(sort: BookmarkSortModel) {
    const options = this.store.selectSnapshot(GalleryState.getSearchOptions);
    options.sortBy = sort;

    this.store.dispatch(new GalleryActions.SetSearchOptions(options));
  }

  handleToggleFilters() {
    const sidenav = this.store.selectSnapshot(GalleryState.getSidenav);
    this.store.dispatch(new GalleryActions.SetSidenav({ 
      ...sidenav,
      open: true
    }));
  }

  handleBulkAction(action: BulkActionEvent) {
    this.bulkAction.next(action);
    this.store.dispatch(new GalleryActions.SetCheckedItems([]));
  }
  
  handleClearQuery() {
    const config = this.store.selectSnapshot(GalleryState.getSearchConfig);
    this.store.dispatch(new GalleryActions.SetSearchConfig({
      ...config,
      query: ''
    }));
  }

  toggleView(viewMode: GalleryViewMode) {
    this.store.dispatch(new GalleryActions.SetViewMode(viewMode));
  }

}
