import { Component, OnInit, ChangeDetectionStrategy, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { Select, Store } from '@ngxs/store';
import { OrganizationsState } from 'src/app/organizations/state/organizations.state';
import { Observable, Subject } from 'rxjs';
import { CollectionModel } from 'src/app/organizations/models/collection.model';
import { OrganizationModel } from 'src/app/organizations/models/organization.model';
import { takeUntil, filter } from 'rxjs/operators';
import { GetCollections } from 'src/app/organizations/state/organizations.actions';
import { isNil } from 'lodash';
import { OrganizationSearchConfig, SearchConfigUnion, PrivateSearchConfig, CollectionSearchConfig } from '../../models/search-config';
import { GalleryActions } from '../../state/gallery.actions';
import { GalleryState } from '../../state/gallery.state';
import { SearchType } from '../../models/search-type.enum';
import { AuthState } from 'src/app/auth/state/auth/auth.state';

@Component({
  selector: 'app-gallery-collections',
  templateUrl: './gallery-collections.component.html',
  styleUrls: ['./gallery-collections.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class GalleryCollectionsComponent implements OnInit, OnDestroy {
  @Select(OrganizationsState.getOrganization)
  organization$: Observable<OrganizationModel>;

  @Select(OrganizationsState.getCollections)
  collections$: Observable<CollectionModel[]>;

  @Select(GalleryState.getSearchConfig)
  searchConfig$: Observable<SearchConfigUnion>;

  @Select(GalleryState.getSearchType)
  searchType$: Observable<SearchType>;

  showBanner = true;
  searchType = SearchType;
  collectionId: number;
  ngUnsubscribe = new Subject();

  constructor(
    private store: Store,
    private changeDetectorRef: ChangeDetectorRef
  ) { }

  ngOnInit() {
    this.observeCollections();
    this.observeSearchConfig();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

  observeCollections() {
    // Update collections when organization updates
    this.organization$
      .pipe(
        filter(organization => !isNil(organization)),
        takeUntil(this.ngUnsubscribe)
      )
      .subscribe((organization) => {
        if (organization) {
          this.store.dispatch(new GetCollections(organization.id));
        }
      });
  }

  observeSearchConfig() {
    this.searchConfig$
      .pipe(
        filter(config => !isNil(config)),
        takeUntil(this.ngUnsubscribe)
      )
      .subscribe(config => {
        // Keep local collectionId up to date for navigation highlighting
        this.collectionId = (config.type === SearchType.Collection) ? config.collectionId : null;

        // Only show organization banner when searching bookmarks
        this.showBanner = config.type === SearchType.Collection
          || config.type === SearchType.Organization
          || (config.type === SearchType.Private && !this.store.selectSnapshot(AuthState.isPrivateUser));

        this.changeDetectorRef.markForCheck();
      });
  }

  handleSelectCollection(collectionId: number) {
    const config = new CollectionSearchConfig(
      this.store.selectSnapshot(OrganizationsState.getOrganization).id,
      collectionId
    );

    this.store.dispatch(new GalleryActions.SetSearchConfig(config));
  }

  handleSelectAll() {
    const config = new OrganizationSearchConfig(
      this.store.selectSnapshot(OrganizationsState.getOrganization).id
    );
    this.store.dispatch(new GalleryActions.SetSearchConfig(config));
  }

  handleSelectPrivate() {
    const config = new PrivateSearchConfig();
    this.store.dispatch(new GalleryActions.SetSearchConfig(config));
  }
}
