import {
  Component,
  OnInit,
  ChangeDetectionStrategy,
  Output,
  EventEmitter,
  Input,
  ChangeDetectorRef,
  OnDestroy,
  ViewChild,
  AfterViewInit,
} from '@angular/core';
import { Store, Select } from '@ngxs/store';
import {
  GalleryViewMode,
  GalleryViewModeDefault,
} from '../../models/gallery-view-mode.enum';
import { BookmarkModel } from 'src/app/bookmarks/models/bookmark.model';
import { GalleryState } from '../../state/gallery.state';
import { Observable, Subject, fromEvent } from 'rxjs';
import { EnvironmentApplication } from 'src/environments/models/environment-application';
import {
  CategorySearchConfig,
  SearchConfigUnion,
  InternalSearchConfig,
  LoftSearchConfig,
  CommunitySearchConfig,
} from '../../models/search-config';
import { GalleryActions } from '../../state/gallery.actions';
import { environment } from 'src/environments/environment';
import { BreakpointObserver } from '@angular/cdk/layout';
import {
  MatSidenav,
  MatSidenavContent,
  MatSidenavContainer,
} from '@angular/material';
import {
  takeUntil,
  distinctUntilChanged,
  debounceTime,
  filter,
  map,
} from 'rxjs/operators';
import {
  tagsLevelOfDetail,
  tagCategories,
  tagsTypes,
} from 'src/app/bookmarks/data/tags';
import { flatten, isEqual, get } from 'lodash';
import { plainToClass } from 'class-transformer';
import { GalleryResultsModel } from '../../models/gallery-results.model';
import { ContentType } from 'src/app/story/models/content-type.enum';
import { GallerySidenav } from '../../state/gallery.state.model';
import { BookmarkActionEvent } from '../../../bookmarks/models/bookmark-action';
import { BulkActionEvent } from '../../models/bulk-action';
import { SearchType } from '../../models/search-type.enum';
import { trigger, transition, style, animate } from '@angular/animations';

@Component({
  selector: 'app-gallery-container',
  templateUrl: './gallery-container.component.html',
  styleUrls: ['./gallery-container.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  animations: [
    trigger('inFromBottom', [
      transition(':enter', [
        style({
          transform: 'translateY(100%)',
        }),
        animate(
          '250ms ease-out',
          style({
            transform: '*',
          })
        ),
      ]),
      transition(':leave', [
        animate(
          '250ms ease-out',
          style({
            transform: 'translateX(100%)',
          })
        ),
      ]),
    ]),
  ],
})
export class GalleryContainerComponent
  implements OnInit, AfterViewInit, OnDestroy {
  applicationEnum = EnvironmentApplication;
  contentTypeEnum = ContentType;
  searchTypeEnum = SearchType;

  @ViewChild(MatSidenavContainer, { static: true, read: MatSidenavContainer })
  sidenavContainer: MatSidenavContainer;

  @ViewChild('sidenav', { static: true, read: MatSidenav })
  sidenav: MatSidenav;

  @ViewChild('content', { static: false, read: MatSidenavContent })
  content: MatSidenavContent;

  @Select(GalleryState.getFilteredResults)
  results$: Observable<GalleryResultsModel>;

  @Select(GalleryState.getCheckedResults)
  checkedResults$: Observable<string[]>;

  @Select(GalleryState.getLoading)
  loading$: Observable<boolean>;

  @Select(GalleryState.getSearchConfig)
  searchConfig$: Observable<SearchConfigUnion>;

  @Select(GalleryState.getSearchType)
  searchType$: Observable<SearchType>;

  @Select(GalleryState.getSearchOptions)
  searchOptions$: Observable<SearchConfigUnion>;

  @Select(GalleryState.getSidenav)
  sideNav$: Observable<GallerySidenav>;

  @Input()
  application: EnvironmentApplication.Studio | EnvironmentApplication.Loft =
    EnvironmentApplication.Studio;

  @Input()
  contentType: ContentType = ContentType.Bookmark;

  @Input()
  viewMode = GalleryViewMode.Card;

  @Input()
  selectLabel = 'Select';

  @Input()
  selectLabelPlural = 'Select';

  @Input()
  showSourcePicker = false;

  @Input()
  lazyLoadThreshold = 480;

  @Input()
  lazyLoadCount = 12;

  activeTags: { [key: string]: string } = {};
  activeConfig: SearchConfigUnion;

  @Output()
  selectBookmark = new EventEmitter<BookmarkModel>();

  @Output()
  bookmarkAction = new EventEmitter<BookmarkActionEvent>();

  @Output()
  bulkAction = new EventEmitter<BulkActionEvent>();

  ngUnsubscribe = new Subject();
  isSmall = true;
  showScrollTo = false;

  tagGroups = [tagsTypes, tagsLevelOfDetail];

  constructor(
    private changeDetectorRef: ChangeDetectorRef,
    private store: Store,
    public breakpointObserver: BreakpointObserver
  ) {}

  ngOnInit() {
    this.initBreakpointObserver();
    this.initSearchConfigObserver();
  }

  ngAfterViewInit() {
    this.store
      .select(GalleryState.getSidenav)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((sidenav) => {
        this.toggleSidenav(sidenav.open && sidenav.enabled);
      });

    this.initLazyLoadObserver();
    this.initScrollObserver();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

  initSearchConfigObserver() {
    this.searchConfig$
      .pipe(
        takeUntil(this.ngUnsubscribe),
        filter((config) => config != null),
        distinctUntilChanged((previousConfig, nextConfig) => {
          return isEqual(previousConfig, nextConfig);
        }),
        debounceTime(100)
      )
      .subscribe((searchConfig) => {
        // Tags come in as a single string array and must be filtered into their component appropriate groups
        if (searchConfig.type === SearchType.Category) {
          // Reset Tags, currently disabled
          tagCategories.forEach((tagGroup) => {
            this.activeTags[tagGroup.title] = '';
          });

          searchConfig.tags.forEach((tag) => {
            const groupId = this.getTagGroup(tag);

            // -- Single tag solution
            this.activeTags[groupId] = tag;

            // -- Reactivate this code once we have multi-tag solution support
            // this.activeTags[groupId] = this.activeTags[groupId] || [];
            // this.activeTags[groupId].push(tag);
          });
        }

        // Reset page count
        const options = this.store.selectSnapshot(
          GalleryState.getSearchOptions
        );
        
        this.store.dispatch(
          new GalleryActions.SetSearchOptions({
            ...options,
            offset: 0,
          })
        );

        this.changeDetectorRef.markForCheck();

        this.search();
      });
  }

  initBreakpointObserver() {
    this.breakpointObserver
      .observe('(max-width: 959px)')
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((result) => {
        this.isSmall = result.matches;

        // Hide the sidenav on smaller resolutions
        const sidenav = this.store.selectSnapshot(GalleryState.getSidenav);
        this.store.dispatch(
          new GalleryActions.SetSidenav({
            ...sidenav,
            open: !this.isSmall,
          })
        );

        // Disable list view on smaller resolutions
        this.store.dispatch(
          new GalleryActions.SetViewMode(GalleryViewModeDefault)
        );

        this.changeDetectorRef.markForCheck();
      });
  }

  initLazyLoadObserver() {
    fromEvent(this.content.getElementRef().nativeElement, 'scroll')
      .pipe(takeUntil(this.ngUnsubscribe), debounceTime(50))
      .subscribe(() => {
        const content = this.content.getElementRef().nativeElement;
        const scrollBottom = content.scrollTop + content.offsetHeight;

        if (content.scrollHeight - scrollBottom <= this.lazyLoadThreshold) {
          const options = this.store.selectSnapshot(
            GalleryState.getSearchOptions
          );
          options.pageSize += this.lazyLoadCount;
          this.store.dispatch(new GalleryActions.SetSearchOptions(options));
        }
      });
  }

  initScrollObserver() {
    this.sidenavContainer.scrollable
      .elementScrolled()
      .pipe(
        takeUntil(this.ngUnsubscribe),
        map((event: any) => {
          return event.srcElement.scrollTop > window.innerHeight;
        }),
        distinctUntilChanged()
      )
      .subscribe((showScrollTo: boolean) => {
        this.showScrollTo = showScrollTo;
        this.changeDetectorRef.markForCheck();
      });
  }

  handleSelect(result: BookmarkModel) {
    this.selectBookmark.emit(result);
  }

  handleCategorySelected(categoryId: string) {
    // Community is included as part of categories, but has a separate endpoints
    const config: SearchConfigUnion =
      categoryId === 'public'
        ? new CommunitySearchConfig('')
        : new CategorySearchConfig(categoryId);

    this.store.dispatch(new GalleryActions.SetSearchConfig(config));

    // On mobile, always collapse filter navigation when picking a category
    if (this.breakpointObserver.isMatched('(max-width: 959px)')) {
      this.toggleSidenav(false);
    }
  }

  handleClientSelected(client: string) {
    // Studio
    if (environment.application === EnvironmentApplication.Studio) {
      const config = plainToClass(InternalSearchConfig, {
        ...this.store.selectSnapshot(GalleryState.getSearchConfig),
        directory: `client/${client}`,
      });

      this.store.dispatch(new GalleryActions.SetSearchConfig(config));
    }
  }

  handleSelectedTagChange(id: string, tag: string) {
    this.activeTags[id] = tag;

    const config = this.store.selectSnapshot(GalleryState.getSearchConfig);

    if (config && config.type === SearchType.Category) {
      const nextConfig = plainToClass(CategorySearchConfig, {
        ...config,
        tags: flatten(Object.values(this.activeTags)),
      });

      this.store.dispatch(new GalleryActions.SetSearchConfig(nextConfig));
    }
  }

  handleLoftConfigChange(config: LoftSearchConfig) {
    this.store.dispatch(new GalleryActions.SetSearchConfig(config));
  }

  handleAction(bookmarkAction: BookmarkActionEvent) {
    this.bookmarkAction.emit(bookmarkAction);
  }

  handleBulkAction(action: BulkActionEvent) {
    this.bulkAction.emit(action);
  }

  toggleSidenav(open?: boolean) {
    this.sidenav.toggle(open);
  }

  getTagGroup(tag: string): string {
    const tagCategory = tagCategories.find((group) =>
      group.tags.some((groupTag) => groupTag === tag)
    );
    return get(tagCategory, 'title', '');
  }

  search() {
    // console.group('Search Studio');
    // console.log(this.store.selectSnapshot(GalleryState.getSearchConfig));
    // console.log(this.store.selectSnapshot(GalleryState.getSearchOptions));
    // console.groupEnd();

    this.store.dispatch(new GalleryActions.SearchStudio());
  }

  scrollToTop() {
    this.content.scrollTo({
      top: 0,
      behavior: 'smooth',
    });
  }
}
