import { Injectable } from '@angular/core';
import { Store, Actions, ofActionCompleted, ActionCompletion } from '@ngxs/store';
import { PublishService, PublishLinkType } from '../publish/publish.service';
import { MatDialog, MatSnackBar } from '@angular/material';
import { DialogService } from '../dialogs/dialog.service';
import { BookmarkModel } from './models/bookmark.model';
import { OrganizationsState } from '../organizations/state/organizations.state';
import { WidgetOptions } from '../publish/models/widget-options.model';
import { PublishState } from '../publish/state/publish.state';
import { AuthState } from '../auth/state/auth/auth.state';
import {
  CopyBookmark,
  DeleteFavorite,
  DeleteBookmark
} from './state/bookmarks.actions';
import { flatMap, take } from 'rxjs/operators';
import {
  PublishDialogComponent,
  PublishDialogData,
} from '../publish/components/publish-dialog/publish-dialog.component';
import { Router } from '@angular/router';
import { BookmarkAction, BookmarkActionEvent } from './models/bookmark-action';
import { isNil } from 'lodash';
import { ContentType } from '../story/models/content-type.enum';
import { BookmarksService } from './bookmarks.service';
import { BreakpointObserver } from '@angular/cdk/layout';
import { environment } from 'src/environments/environment';
import { EnvironmentPlatform } from 'src/environments/models/environment-platform';
import { EnvironmentApplication } from 'src/environments/models/environment-application';
import { DialogActions } from '../dialogs/state/dialog.actions';
import { StoryService } from '../story/story.service';
import { ClipboardService } from '../shared/clipboard.service';

const loftActions = [
  BookmarkAction.View,
  BookmarkAction.Edit
];

@Injectable({
  providedIn: 'root',
})
export class BookmarkActionsService {
  constructor(
    private store: Store,
    private publishService: PublishService,
    private dialogService: DialogService,
    private dialog: MatDialog,
    private snackbar: MatSnackBar,
    private router: Router,
    private bookmarksService: BookmarksService,
    private breakpointObserver: BreakpointObserver,
    private clipboardService: ClipboardService
  ) {}

  getBookmarkActions(
    bookmark: BookmarkModel,
    isPremiumUser: boolean,
    isPrivateUser: boolean,
    options?: {
      excludeActions?: BookmarkAction[],
      includeActions?: BookmarkAction[]
    }
  ): BookmarkAction[] {
    // If no bookmark is provided, return nothing
    if (isNil(bookmark)) {
      return [];
    }

    // When a "bookmark" content type is flagged as 'public' (a Community bookmark) we want to treat
    // it as a module when not owned by the user. 
    // This prevents 3rd party users from seeing bookmark management options.
    const tags = bookmark.tags || [];
    const bookmarkType = (tags.includes('public') && !bookmark.is_owner) ? 
      ContentType.Module : bookmark.type;

    let actions: BookmarkAction[] = [];
    switch (bookmarkType) {
      case ContentType.Module:
        // custom client "modules" are enabled for sharing/publishing
        const isCustomUserModel = !isNil(bookmark.module_accessor_level);
        // collection "modules" are enabled for sharing/publishing
        const isCollectionContent =  bookmark.collection_content === true;

        const shareOptions = isCustomUserModel || isCollectionContent
          ? [
            BookmarkAction.Divider,
            BookmarkAction.CopyURL,
            BookmarkAction.Publish
          ]
          : [];

        // for custom models, check if "add to" actions applicable (i.e., hide add to team if assigned to team already)
        const addToActions = [
          ...bookmark.module_accessor_level === 'user' ? [] : [BookmarkAction.AddToMyModels],
          ...bookmark.module_accessor_level === 'group' ? [] : [BookmarkAction.AddToTeamModels]
        ];

        actions = [
          BookmarkAction.View,
          BookmarkAction.Edit,
          ...shareOptions,
          BookmarkAction.Divider,
          ...addToActions
        ];
        break;

      case ContentType.Bookmark:
        actions = [
          BookmarkAction.View,
          BookmarkAction.Edit,
          BookmarkAction.Divider,
          BookmarkAction.CopyURL,
          BookmarkAction.Publish,
          BookmarkAction.Divider,
          BookmarkAction.Manage,
          BookmarkAction.Delete,
          BookmarkAction.Divider,
          BookmarkAction.AddToMyModels,
        ];
        break;
    }

    actions = actions.filter((bookmarkAction) => {
      // Restrict Loft actions
      if (environment.application === EnvironmentApplication.Loft && !loftActions.includes(bookmarkAction)) {
        return false;
      }

      // Filter out any excluded actions
      if (options.excludeActions && options.excludeActions.includes(bookmarkAction)) {
        return false;
      }

      // Filter out only included actions
      if (options.includeActions && !options.includeActions.includes(bookmarkAction)) {
        return false;
      }

      switch (bookmarkAction) {
        case BookmarkAction.View:
        case BookmarkAction.Edit:
          return (bookmark.premium ? isPremiumUser : true);

        case BookmarkAction.Delete:
          return bookmark.is_owner;

        case BookmarkAction.AddToMyModels:
          return (
            !bookmark.is_owner && (bookmark.premium ? isPremiumUser : true)
          );

        case BookmarkAction.AddToTeamModels:
          return !isPrivateUser;

        case BookmarkAction.Manage:
          return this.bookmarksService.canEdit(bookmark);

        default:
          return true;
      }
    });

    // Certain actions are excluded from the small site due to lack of UI support.
    // This is subject to change as we continue to grow the mobile experience.
    const isSmall = this.breakpointObserver.isMatched('(max-width: 959px)');
    const excludedActions = [
      BookmarkAction.Edit,
      BookmarkAction.Publish,
      BookmarkAction.Manage,
    ];

    if (isSmall || environment.platform !== EnvironmentPlatform.Web) {
      actions = actions.filter((action) => {
        return (
          !excludedActions.includes(action) && action !== BookmarkAction.Divider
        );
      });
    }

    // Clean up stray dividers
    while (actions[0] === BookmarkAction.Divider) {
      actions.splice(0, 1);
    }
    while (actions[actions.length - 1] === BookmarkAction.Divider) {
      actions.pop();
    }

    return actions;
  }

  handleBookmarkAction(action: BookmarkActionEvent) {
    const bookmark = action.bookmark as BookmarkModel;

    switch (action.name) {
      case BookmarkAction.View:
        this.view(bookmark);
        break;

      case BookmarkAction.Edit:
        this.router.navigate(['/edit'], { queryParams: { id: bookmark.id } });
        break;

      case BookmarkAction.Manage:
        this.manage(bookmark);
        break;

      case BookmarkAction.Delete:
        this.delete(bookmark);
        break;

      case BookmarkAction.CopyURL:
        let _url: string;

        this.generateBookmarkLink(bookmark)
          .pipe(
            flatMap((url) => {
              _url = url;
              return this.clipboardService.copyToClipboard(url);
            })
          )
          .subscribe(copied => {
            if (copied) {
              this.snackbar.open('Successfully copied link to clipboard.');
            } else {
              this.dialogService.openClipboardDialog(_url);
            }
          });
        break;

      case BookmarkAction.AddToMyModels:
        this.addToPrivate(bookmark);
        break;

      case BookmarkAction.AddToTeamModels:
        this.addToTeam(bookmark);
        break;

      case BookmarkAction.Publish:
        this.publish(bookmark);
        break;
    }
  }

  canEdit(bookmark: BookmarkModel, isPremiumUser: boolean): boolean {
    return (bookmark.premium ? isPremiumUser : true);
  }

  generateBookmarkLink(bookmark: BookmarkModel) {
    const widgetOptions =
      this.store.selectSnapshot(OrganizationsState.getWidgetOptions) ||
      new WidgetOptions();
    const widgetURL = this.store.selectSnapshot(PublishState.getWidgetURL);
    const lang = this.store.selectSnapshot(AuthState.getUserLanguage);
    const app = this.store.selectSnapshot(AuthState.getSelectedUserApp);

    // get url
    return this.publishService.generateWidgetURL(
      bookmark,
      widgetURL,
      widgetOptions,
      null,
      PublishLinkType.Single,
      lang,
      app
    );
  }

  addToPrivate(bookmark: BookmarkModel) {
    const lang = this.store.selectSnapshot(AuthState.getUserLocale).lang;
    this.store.dispatch(new CopyBookmark(bookmark.id, bookmark.type, lang));
  }

  addToTeam(bookmark: BookmarkModel) {
    this.dialogService.openAddTeamModelsDialog([bookmark]);
  }

  delete(bookmark: BookmarkModel) {
    const dialogRef = this.dialogService.openConfirmDialog({
      data: {
        titleText: 'Delete 3D model',
        bodyText: 'Are you sure you want to delete this 3D model?',
        confirmText: 'Delete',
        confirmColor: 'warn',
      },
    });

    dialogRef
      .afterClosed()
      .pipe(take(1))
      .subscribe((confirmed) => {
        if (confirmed) {
          // special case: favorites
          const isFavorite = bookmark.favorite;
          if (isFavorite) {
            this.store.dispatch(new DeleteFavorite(bookmark));
          } else {
            this.store.dispatch(new DeleteBookmark(bookmark.id));
          }
        }
      });
  }

  manage(bookmark: BookmarkModel) {
    this.dialogService.openManageDialog(bookmark);
  }

  view(bookmark: BookmarkModel) {
    const { id, title } = bookmark;

    const lang = this.store.selectSnapshot(AuthState.getUserLocale).lang;
    const isCustomWidget = this.isCustomWidget(bookmark);

    // Premium
    const isPremium =
      bookmark.premium && !this.store.selectSnapshot(AuthState.isPremiumUser);
    if (isPremium) {
      this.store.dispatch(new DialogActions.OpenPremiumContent());

      // Unavailable
    } else if (bookmark.unavailable) {
      this.snackbar.open(`Error opening "${title}", it is unavailable.`);

      // Custom Widget
    } else if (isCustomWidget) {
      const url = this.publishService.getWidgetURL(
        bookmark,
        bookmark.embed_url,
        new WidgetOptions()
      );
      const handle = window.open(url);

      handle
        ? this.snackbar.open(`"${title}" loaded in a new tab.`)
        : this.snackbar.open(`Error launching "${title}" in a new tab.`);

      // Normal Bookmark
    } else {
      this.router.navigate(['/view'], { queryParams: { id, lang } });
    }
  }

  publish(bookmark: BookmarkModel) {
    this.dialog.open(PublishDialogComponent, {
      width: '90vw',
      height: '95vh',
      data: <PublishDialogData>{
        bookmark: bookmark,
      },
    });
  }

  private isCustomWidget(bookmark: BookmarkModel) {
    return this.publishService.isCustomWidget(bookmark.embed_url_path);
  }
}
