import { Component, OnInit, ChangeDetectionStrategy, ViewEncapsulation, Input, ViewChild, ElementRef, Renderer2 } from '@angular/core';
import { getAllCategories } from '../../features/site-navigation/site-navigation-utils';

const imageMap = {
  'all': 'all',
  'complete anatomy': 'complete-anatomy',
  'system': 'system-anatomy',
  'regional': 'regional-anatomy',
  'Allergy and Immunology': 'allergy',
  'Cardiology': 'cardiology',
  'Cross Section and Micro Anatomy': 'cross-sections',
  'Dentistry': 'dentistry',
  'Dermatology': 'dermatology',
  'Endocrinology': 'endocrinology',
  'Gastroenterology and Hepatology': 'gastroenterology',
  'Infectious Disease': 'infectious',
  'Nephrology': 'nephrology',
  'Neurology and Psychiatry': 'neurology',
  'Obstetrics and Gynecology': 'obstetrics',
  'Oncology and Hematology': 'oncology',
  'Ophthalmology': 'ophthalmology',
  'Orthopedics': 'orthopedics',
  'Otolaryngology': 'otolaryngology',
  'Pediatrics': 'pediatrics',
  'Pulmonology and Respiratory': 'pulmonology',
  'Rheumatology': 'rheumatology',
  'Urology': 'urology',

  'public': 'community'
};


export type categoryColor = 'mono' | 'color';
@Component({
  selector: 'app-category-image',
  templateUrl: './category-image.component.html',
  styleUrls: ['./category-image.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class CategoryImageComponent implements OnInit {
  @ViewChild('image', { static: true })
  imageRef: ElementRef<HTMLDivElement>;

  @Input()
  public get active() {
    return this._active;
  }
  public set active(value) {
    this._active = value;
    this.updateImage();
  }
  private _active = false;

  @Input()
  public get category() {
    return this._category;
  }
  public set category(value) {
    this._category = value;
    this.updateImage();
  }
  private _category = '';

  @Input()
  public get color(): categoryColor {
    return this._color;
  }
  public set color(value: categoryColor) {
    this._color = value;
    this.updateImage();
  }
  private _color: categoryColor = 'mono';

  cssClass = '';
  allCategories = getAllCategories();

  constructor(
  ) { }

  ngOnInit() {
  }

  private updateImage() {
    const result = this.allCategories.find((value) => {
      return value.id === this.category || value.title.toLowerCase() === this.category.toLowerCase();
    });

    if (result) {
      const imageId = imageMap[result.id];
      this.cssClass = `category-image--${this.color}-${imageId}`;
    }
  }
}
