import { ChangeDetectionStrategy, Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { ImageBlock, ImagesBlock } from '../../models/images-block';
import { ContentBlockLayout, IContentBlockControl } from '../../models/content-block-control';

const compactImageSize = 100;

@Component({
  selector: 'app-images-block',
  templateUrl: './images-block.component.html',
  styleUrls: ['./images-block.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None
})
export class ImagesBlockComponent implements OnInit, IContentBlockControl {

  @Input()
  imagesBlock: ImagesBlock;

  @Input()
  layout = ContentBlockLayout.Normal;

  images: ImageBlock[];
  currentImage: ImageBlock;

  get fit(): 'normal' | 'auto' {
    return this.layout === 'compact' ? 'auto' : 'normal';
  }

  get fitSize(): number {
    return this.layout === 'compact' ? compactImageSize : undefined;
  }

  constructor() { }

  ngOnInit() {
    this.images = this.imagesBlock.images;
    this.currentImage = this.images[0];
  }

  handlePrevClick() {
    let currentImageIndex = this.getCurrentImageIndex();
    currentImageIndex--;

    if (currentImageIndex < 0) {
      currentImageIndex = this.images.length - 1;
    }

    this.currentImage = this.images[currentImageIndex];
  }

  handleNextClick() {
    let currentImageIndex = this.getCurrentImageIndex();
    currentImageIndex++;

    if (currentImageIndex >= this.images.length) {
      currentImageIndex = 0;
    }

    this.currentImage = this.images[currentImageIndex];
  }

  getCurrentImageIndex() {
    return this.images.indexOf(this.currentImage);
  }

  hasImages() {
    return this.images.length > 1;
  }

}
