import { ChangeDetectionStrategy, ChangeDetectorRef, Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MatSnackBar, MAT_DIALOG_DATA } from '@angular/material';
import { Actions, ofActionDispatched, Select, Store } from '@ngxs/store';
import { Observable, of } from 'rxjs';
import { Subject } from 'rxjs/internal/Subject';
import { takeUntil } from 'rxjs/operators';
import { OrganizationModel } from 'src/app/organizations/models/organization.model';
import { OrganizationInviteResponse } from 'src/app/organizations/organizations.service';
import { SendOrganizationInvite, SendOrganizationInviteFail, SendOrganizationInviteSuccess } from 'src/app/organizations/state/organizations.actions';
import { OrganizationsState } from 'src/app/organizations/state/organizations.state';
import { TextUtils } from 'src/app/shared/libraries/text-utils/text-utils';

const maxEmailCount = 50;

export interface TeamInviteDialogData {
  organization: OrganizationModel;
}

@Component({
  selector: 'app-team-invite-dialog',
  templateUrl: './team-invite-dialog.component.html',
  styleUrls: ['./team-invite-dialog.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class TeamInviteDialogComponent implements OnInit, OnDestroy {
  maxEmailCount = maxEmailCount;

  @Select(OrganizationsState.getInviting)
  inviting$: Observable<boolean>;

  form: FormGroup;
  private ngUnsubscribe = new Subject();
  private organization: OrganizationModel;

  // state
  sent = false;
  sentResponses: OrganizationInviteResponse[] = [];
  errorResponses: OrganizationInviteResponse[] = [];

  constructor(
    private store: Store,
    private actions: Actions,
    private formBuilder: FormBuilder,
    private dialogRef: MatDialogRef<TeamInviteDialogComponent>,
    @Inject(MAT_DIALOG_DATA) private data: TeamInviteDialogData,
    private changeRef: ChangeDetectorRef,
    private snackBar: MatSnackBar
  ) {
    this.organization = data.organization;
  }

  ngOnInit() {
    this.initForm();
    this.initInviteListeners();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

  initForm() {
    this.form = this.formBuilder.group({
      'emails': ['', Validators.required, this.emailValidator]
    });
  }

  initInviteListeners() {
    // inviting
    this.actions
      .pipe(
        ofActionDispatched(SendOrganizationInvite),
        takeUntil(this.ngUnsubscribe)
      )
      .subscribe(() => {
        this.sentResponses = [];
        this.errorResponses = [];
        this.changeRef.markForCheck();
      });

    // NOTE: errors in sending are retured in a successful response
    this.actions
      .pipe(
        ofActionDispatched(SendOrganizationInviteSuccess),
        takeUntil(this.ngUnsubscribe)
      )
      .subscribe(({ responses }: SendOrganizationInviteSuccess) => {
        this.sentResponses = responses
          .filter((r) => r.status === 'success');

        this.errorResponses = responses
          .filter((r) => r.status === 'error');

        this.sent = this.errorResponses.length === 0;

        // auto close
        if (this.sent) {
          setTimeout(() => {
            this.dialogRef.close();
          }, 5000);
        }

        this.changeRef.markForCheck();
      });

    // error: server
    this.actions
      .pipe(
        ofActionDispatched(SendOrganizationInviteFail),
        takeUntil(this.ngUnsubscribe)
      )
      .subscribe((action: SendOrganizationInviteFail) => {
        this.sent = false;

        this.sentResponses = [];
        this.errorResponses = [];

        this.snackBar.open('There was an error sending the invites, please try again.');

        this.changeRef.markForCheck();
      });
  }

  handleCancelClick() {
    this.dialogRef.close(false);
  }

  backToFormClick() {
    this.resetState();
    this.changeRef.markForCheck();
  }

  handleInviteClick() {
    const { eid } = this.organization;
    const emails = TextUtils.parseEmails(this.form.value.emails);

    this.store.dispatch(new SendOrganizationInvite(eid, emails));
  }

  formatEmails(emails: string[]): string {
    return emails.join(', ');
  }

  formatResponses(responses: OrganizationInviteResponse[]): string[] {
    // group reasons for easier to output messages
    const reasonGroups = responses.map((r) => r.reason);
    const distinctReasons = [
      ...new Set([...reasonGroups])
    ];

    // 'Reason for error: email1@biodigital.com, email2@biodigital.com' 
    return distinctReasons.map((reason) => {
      const emails = responses.filter((r) => r.reason === reason)
        .map((r) => r.email);
      const formattedEmails = this.formatEmails(emails);
      return `"${reason}": ${formattedEmails}`;
    });
  }

  private resetState() {
    this.sent = false;
    this.sentResponses = [];
    this.errorResponses = [];
  }

  private emailValidator(control: FormControl) {
    const emails = TextUtils.parseEmails(control.value);
    const invalidEmails = emails
      .filter((e) => !TextUtils.validateEmail(e));
    const currentEmailCount = emails.length;
    const maxEmailsExceeded = currentEmailCount > maxEmailCount;

    let errors = {};

    // validate: max length
    if (maxEmailsExceeded) {
      errors = {
        ...errors,
        maxEmailsExceeded,
        currentEmailCount
      };
    }

    // validate: email format
    if (invalidEmails.length > 0) {
      errors = {
        ...errors,
        invalidEmails
      };
    }

    if (Object.keys(errors).length > 0) { // fail validation
      return of(errors);
    } else { // pass validation
      return of(null);
    }


  }

}
