import {BdGraphNode, BdGraph, BdGraphNodeFactory} from '../../../lib/graph/graph.model';

// Model for node available in state for user interaction with scene graph

export interface ViewTreeNode extends BdGraphNode {
    label: string;
    description: string;
    
    boolState: {
        pickable: boolean;
        renderable: boolean;
        shown: boolean;
        expanded: boolean;
    };

    numState: {
      layer: number;
    };

}

export function ViewTreeNodeFactory(data: any): ViewTreeNode {
    return {
        // inherited props:
        ...BdGraphNodeFactory(data.id),

        // view tree node props:
         label : data.label || '',
         description : data.description,

         boolState: {
             pickable: data.hasOwnProperty('pickable') ? data.pickable : true,
             renderable: data.renderable || false,
             shown : data.shown || false,
             expanded : data.expanded || false,
         },

         numState: {
             layer: data.layer
         }
    };
}
