import {
  Component,
  OnInit,
  ChangeDetectionStrategy,
  OnDestroy,
  ChangeDetectorRef,
  Output,
  EventEmitter,
} from '@angular/core';
import { SiteCategory } from 'src/app/shared/features/site-navigation/models/site-category.model';
import { Select } from '@ngxs/store';
import { GalleryState } from '../../state/gallery.state';
import { Observable, Subject } from 'rxjs';
import {
  SearchConfigUnion,
  CategorySearchConfig,
  CommunitySearchConfig,
} from '../../models/search-config';
import { takeUntil, distinctUntilChanged, filter } from 'rxjs/operators';
import { SearchType } from '../../models/search-type.enum';
import {
  translateCategories,
  getAllCategories,
} from 'src/app/shared/features/site-navigation/site-navigation-utils';
import { AuthState } from 'src/app/auth/state/auth/auth.state';
import { isNil } from 'lodash';

@Component({
  selector: 'app-gallery-categories',
  templateUrl: './gallery-categories.component.html',
  styleUrls: ['./gallery-categories.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class GalleryCategoriesComponent implements OnInit, OnDestroy {
  categories: SiteCategory[] = [];

  @Select(GalleryState.getSearchConfig)
  searchConfig$: Observable<SearchConfigUnion>;

  @Select(AuthState.getUserLanguage)
  language$: Observable<string>;

  @Output()
  categorySelected = new EventEmitter<string>();

  categoryId: string;
  ngUnsubscribe = new Subject();

  constructor(private changeDetectorRef: ChangeDetectorRef) {}

  ngOnInit() {
    this.initCategories();
    this.observeCategoryId();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

  initCategories() {
    this.language$
      .pipe(distinctUntilChanged(), takeUntil(this.ngUnsubscribe))
      .subscribe((language) => {
        const categories = getAllCategories();
        this.categories = translateCategories(categories, language);
      });
  }

  handleSelectCategory(categoryId: string) {
    this.categorySelected.emit(categoryId);
  }

  observeCategoryId() {
    this.searchConfig$
      .pipe(
        takeUntil(this.ngUnsubscribe),
        filter(
          (config) =>
            !isNil(config) &&
            (config.type === SearchType.Category || config.type === SearchType.Community)
        )
      )
      .subscribe((config: CategorySearchConfig | CommunitySearchConfig) => {
        this.categoryId = config.categoryId;
        this.changeDetectorRef.markForCheck();
      });
  }
}
