import * as Rollbar from 'rollbar';
import { InjectionToken, Injectable, ErrorHandler, Inject } from '@angular/core';
import { environment } from 'src/environments/environment';
import { EnvironmentDeploy } from 'src/environments/models/environment-deploy';


const rollbarConfig: Rollbar.Configuration = {
  accessToken: environment.rollbarToken,
  captureUncaught: true,
  captureUnhandledRejections: true,
  payload: {
    environment: environment.deploy
  },
  ignoredMessages: [
    'asset not found',
    'this._setCompressedTextureImage is not a function'
  ],
  // enabled: environment.deploy !== EnvironmentDeploy.Local // Disable during development
  enabled: false
};

export const RollbarService = new InjectionToken<Rollbar>('rollbar');

@Injectable()
export class RollbarErrorHandler implements ErrorHandler {
  constructor(@Inject(RollbarService) private rollbar: Rollbar) {}

  handleError(err: any): void {
    // Still log errors to console when developing
    if (environment.deploy !== EnvironmentDeploy.Production) {
      console.error(err);
    }

    this.rollbar.error(err.originalError || err);
  }
}

export function rollbarFactory() {
    return new Rollbar(rollbarConfig);
}
