export interface RawParams {
  [prop: string]: string;
}

interface ParamTransform {
  [prop: string]: (params: RawParams) => any;
}

type ParamValueTypes = string | number | boolean | BackgroundModel | CameraModel;
type ValueTransformer = (input: string) => ParamValueTypes;

interface ParamValueTransform {
  [prop: string]: ValueTransformer;
}

