import { 
  Component,
  OnInit,
  ChangeDetectionStrategy,
  Input,
  ChangeDetectorRef,
  Output,
  EventEmitter, 
  OnDestroy,
  ViewChild,
  ElementRef} from '@angular/core';
import { environment } from 'src/environments/environment';
import { GalleryViewMode } from '../../models/gallery-view-mode.enum';
import { BookmarkModel } from 'src/app/bookmarks/models/bookmark.model';
import { Select, Store } from '@ngxs/store';
import { GalleryState } from '../../state/gallery.state';
import { Observable, BehaviorSubject, Subject, combineLatest } from 'rxjs';
import { BookmarkSortModel } from 'src/app/bookmarks/models/bookmark-sort.model';
import { GalleryActions } from '../../state/gallery.actions';
import { SearchOptions } from '../../models/search-options';
import { BookmarkActionEvent } from '../../../bookmarks/models/bookmark-action';
import { GalleryResultsModel } from '../../models/gallery-results.model';
import { SearchType } from '../../models/search-type.enum';
import { takeUntil, debounceTime, filter, map, withLatestFrom, combineAll, tap } from 'rxjs/operators';
import { SearchConfigUnion } from '../../models/search-config';
import { isNil } from 'lodash';

@Component({
  selector: 'app-gallery-results',
  templateUrl: './gallery-results.component.html',
  styleUrls: ['./gallery-results.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class GalleryResultsComponent implements OnInit, OnDestroy {
  mobileQuery: MediaQueryList;
  galleryViewMode = GalleryViewMode;

  @ViewChild('body', { static: true })
  body: ElementRef<HTMLDivElement>;
  
  @Input()
  loading = false;

  @Input()
  viewMode = GalleryViewMode.Card;

  @Input()
  selectLabel = 'Select';

  @Input()
  selectLabelPlural = 'Select';

  @Input()
  public get results(): GalleryResultsModel {
    return this._results.value;
  }
  public set results(value: GalleryResultsModel) {
    this._results.next(value);
    this.changeDetectorRef.markForCheck();
  }
  private _results = new BehaviorSubject<GalleryResultsModel>({ total: 0, items: []});

  @Input()
  checkable = false;

  @Input()
  checkedResults: string[] = [];

  @Output()
  selectBookmark = new EventEmitter<BookmarkModel>();

  @Output()
  checkedChange = new EventEmitter<string[]>();

  @Output()
  bookmarkAction = new EventEmitter<BookmarkActionEvent>();

  @Select(GalleryState.getSearchOptions)
  searchOptions$: Observable<SearchOptions>;

  @Select(GalleryState.getSortOptions)
  sortOptions$: Observable<BookmarkSortModel>;

  @Select(GalleryState.getSort)
  sort$: Observable<BookmarkSortModel>;
  

  emptyMessage: 'none' | 'default' | 'team' = 'default';
  ngUnsubscribe = new Subject();

  constructor(
    private changeDetectorRef: ChangeDetectorRef,
    private store: Store
  ) { }

  ngOnInit() {
    // Observe results and config, determine which custom 'no results' message to show
    combineLatest(
      this.store.select(GalleryState.getSearchConfig),
      this._results
    ).pipe(
      tap(() => {
        this.emptyMessage = 'none';
        this.changeDetectorRef.markForCheck();
      }),
      debounceTime(100),
      takeUntil(this.ngUnsubscribe),
      filter(([searchConfig, results]) => !isNil(searchConfig) && results.total === 0),
    ).subscribe(([searchConfig, results]) => {
      this.updateEmptyMessage(searchConfig, results);
      this.changeDetectorRef.markForCheck();
    });
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

  handleSelect(result: BookmarkModel) {
    this.selectBookmark.emit(result);
  }

  handleCheckedChange(ids: string[]) {
    this.checkedChange.emit(ids);
  }

  handleBookmarkAction(bookmarkAction: BookmarkActionEvent) {
    this.bookmarkAction.emit(bookmarkAction);
  }

  handleSort(sort: BookmarkSortModel) {
    const options = this.store.selectSnapshot(GalleryState.getSearchOptions);
    options.sortBy = sort;

    this.store.dispatch(new GalleryActions.SetSearchOptions(options));
  }

  updateEmptyMessage(searchConfig: SearchConfigUnion, results: GalleryResultsModel) {
    this.emptyMessage = 'default';

    // Team Message
    if (
      searchConfig.query === '' &&
      (searchConfig.type === SearchType.Collection || searchConfig.type === SearchType.Organization || searchConfig.type === SearchType.Private) &&
      results.total === 0
    ) {
      this.emptyMessage = 'team';
    }
  }

  scrollToTop() {
    this.body.nativeElement.scrollTo({
      top: 0
    });
  }
}
