import { Component, OnInit, ChangeDetectionStrategy, Inject, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Select, Store, Actions, ofActionDispatched } from '@ngxs/store';
import { OrganizationsState } from 'src/app/organizations/state/organizations.state';
import { Observable, Subject } from 'rxjs';
import { CollectionModel } from 'src/app/organizations/models/collection.model';
import { BookmarkModel } from 'src/app/bookmarks/models/bookmark.model';
import { CopyBookmark, CopyBookmarkSuccess, CopyBookmarkFailure } from 'src/app/bookmarks/state/bookmarks.actions';
import { ContentType } from 'src/app/story/models/content-type.enum';
import { takeUntil, filter } from 'rxjs/operators';
import { GetCollections } from 'src/app/organizations/state/organizations.actions';
import { BookmarkGroupModel } from 'src/app/bookmarks/models/bookmark.group.model';
import { SaveModuleDialogComponent } from '../save-module-dialog/save-module-dialog.component';
import { BookmarksState } from 'src/app/bookmarks/state/bookmarks.state';
import { StorageService, StorageItemEnum } from 'src/app/shared/storage.service';
import { AuthState } from 'src/app/auth/state/auth/auth.state';
import { cloneDeep, get } from 'lodash';

export interface AddTeamModelDialog {
  bookmarks: BookmarkModel[];
}

@Component({
  selector: 'app-add-team-model-dialog',
  templateUrl: './add-team-model-dialog.component.html',
  styleUrls: ['./add-team-model-dialog.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class AddTeamModelDialogComponent implements OnInit, OnDestroy {

  form: FormGroup;
  settings: AddTeamModelDialog;

  @Select(OrganizationsState.getCollections)
  collections$: Observable<CollectionModel[]>;

  @Select(BookmarksState.getCopying)
  copying$: Observable<boolean>;

  ngUnsubscribe = new Subject();
  bookmarkIterator: BookmarkModel[];
  newBookmarkIds: string[] = [];
  copyBookmarkNumber = 0;
  copyBookmarkPercent = 0;

  constructor(
    private store: Store,
    private actions: Actions,
    private formBuilder: FormBuilder,
    private dialogRef: MatDialogRef<SaveModuleDialogComponent>,
    private snackBar: MatSnackBar,
    private storageService: StorageService,
    @Inject(MAT_DIALOG_DATA) public data: AddTeamModelDialog
  ) { 
    this.settings = {...data};
    const organization = this.store.selectSnapshot(OrganizationsState.getOrganization);
    if (organization) {
      this.store.dispatch(new GetCollections(organization.id));
    }
  }

  ngOnInit() {
    this.initForm();
    this.initObservers();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

  handleSubmit() {
    // 1. Pop first bookmark from list
    // 2. Add to collection
    // 3. Wait for success
    // 4. If more items in list, repeat

    if (this.form.valid) {
      this.bookmarkIterator = [...this.settings.bookmarks];
      this.copyNextBookmark();
    }
  }

  copyNextBookmark() {
    const bookmark = this.bookmarkIterator.pop();
    this.copyBookmarkNumber = this.settings.bookmarks.length - this.bookmarkIterator.length;
    this.copyBookmarkPercent = ((this.copyBookmarkNumber - 1) / this.settings.bookmarks.length) * 100;
    this.addTeamModel(bookmark);
  }

  handleCopySuccess(action: CopyBookmarkSuccess) {
    const sourceBookmark = this.settings.bookmarks.find(value => value.id === action.sourceId);
    const collection = get(action, 'collection.collection_name', 'Team Models');

    this.snackBar.open(`Added ${sourceBookmark.title} to ${collection}.`);
    this.newBookmarkIds.push(action.newId);

    if (this.bookmarkIterator.length) {
      this.copyNextBookmark();
    } else {
      this.dialogRef.close(this.newBookmarkIds);
    }
  }

  handleCopyFailure(action: CopyBookmarkFailure) {
    const sourceBookmark = this.settings.bookmarks.find(value => value.id === action.id);
    this.snackBar.open(`Adding ${sourceBookmark.title} to your team models has failed. Please try again.`);
  }

  initForm() {
    // read defaults
    const defaultGroupEdit = this.storageService.getLocalItem(StorageItemEnum.DefaultGroupEdit) === 'true';
    const defaultCollectionId = this.storageService.getLocalItem(StorageItemEnum.DefaultCollectionId);

    this.form = this.formBuilder.group({
      groupEditEnabled: [`${defaultGroupEdit}`, Validators.required],
      collectionId: [defaultCollectionId]
    });

    // write defaults
    this.form.valueChanges
      .pipe(
        takeUntil(this.ngUnsubscribe)
      )
      .subscribe(({ groupEditEnabled, collectionId }) => {
        if (groupEditEnabled) {
          this.storageService.setLocalItem(StorageItemEnum.DefaultGroupEdit, groupEditEnabled);
        } else {
          this.storageService.removeLocalItem(StorageItemEnum.DefaultGroupEdit);
        }
        if (collectionId) {
          this.storageService.setLocalItem(StorageItemEnum.DefaultCollectionId, collectionId);
        } else {
          this.storageService.removeLocalItem(StorageItemEnum.DefaultCollectionId);
        }
      });
  }

  initObservers() {
    // Success
    this.actions
      .pipe(
        ofActionDispatched(CopyBookmarkSuccess),
        takeUntil(this.ngUnsubscribe)
      )
      .subscribe((action) => {
        this.handleCopySuccess(action);
      });

    // Failure
    this.actions
      .pipe(
        ofActionDispatched(CopyBookmarkFailure),
        takeUntil(this.ngUnsubscribe)
      )
      .subscribe((action) => {
        this.handleCopyFailure(action);
      });
  }

  addTeamModel(bookmark: BookmarkModel) {
    const { groupEditEnabled, collectionId } = this.form.value;
    const organization = this.store.selectSnapshot(OrganizationsState.getOrganization);
    const collections = this.store.selectSnapshot(OrganizationsState.getCollections);
    const lang = this.store.selectSnapshot(AuthState.getUserLocale).lang;
    
    // set group access    
    const groupAccess: BookmarkGroupModel = {
      group_id: organization.id,
      member_edit: groupEditEnabled === 'true',
      visible: true
    };

    // set collection
    const collection = collections.find(({ collection_id }) => `${collection_id}` === collectionId);

    // copy bookmark
    this.store.dispatch(new CopyBookmark(bookmark.id, <ContentType>bookmark.type, lang, groupAccess, collection));
  }
 
}
