import { 
  Component,
  OnInit,
  Input,
  ChangeDetectionStrategy,
  Output,
  EventEmitter,
  ViewEncapsulation } from '@angular/core';
import { BookmarkModel } from 'src/app/bookmarks/models/bookmark.model';
import { BookmarkActionEvent } from 'src/app/bookmarks/models/bookmark-action';
import { GalleryViewMode } from 'src/app/gallery/models/gallery-view-mode.enum';
import { Select } from '@ngxs/store';
import { GalleryState } from 'src/app/gallery/state/gallery.state';
import { Observable } from 'rxjs';
import { GalleryResultsModel } from 'src/app/gallery/models/gallery-results.model';

@Component({
  selector: 'app-gallery-cards',
  templateUrl: './gallery-cards.component.html',
  styleUrls: ['./gallery-cards.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None
})
export class GalleryCardsComponent implements OnInit {
  galleryViewMode = GalleryViewMode;

  @Select(GalleryState.getViewMode)
  viewMode$: Observable<GalleryViewMode>;

  @Output()
  bookmarkAction = new EventEmitter<BookmarkActionEvent>();

  @Input()
  selectLabel = 'Select';

  @Input()
  selectLabelPlural = 'Select';

  @Input()
  public get results(): GalleryResultsModel {
    return this._results;
  }
  public set results(value: GalleryResultsModel) {
    this._results = value;
  }
  private _results: GalleryResultsModel;

  @Input()
  checkable = false;

  @Input()
  checkedResults: string[] = [];

  @Output()
  selectBookmark = new EventEmitter<BookmarkModel>();


  constructor() { }

  ngOnInit() {
  }

  handleSelect(result: BookmarkModel) {
    this.selectBookmark.emit(result);
  }

  handleBookmarkAction(bookmarkAction: BookmarkActionEvent) {
    this.bookmarkAction.emit(bookmarkAction);
  }

  isChecked(id: string) {
    return this.checkedResults.indexOf(id) >= 0;
  }

  trackCard(result: any) {
    return result.id;
  }
}
