import { State, Store, Action, StateContext, Selector, createSelector } from '@ngxs/store';
import { 
  SaveModule,
  SaveModuleSuccess,
  SaveModuleFailure,
  ResetModules,
  SetSaveModule,
  GetModulePrimaryCategories,
  GetModuleSecondaryCategories,
  DuplicateModuleSuccess,
  DuplicateModuleFailure} from './modules.actions';
import { SerializeModuleService } from 'src/app/edit/serialize-module.service';
import { TrimConfiguration } from 'src/app/edit/models/trim-config.model';
import { ModulesService } from '../modules.service';
import { ModulesSaveResultEnum } from '../models/modules-save-result.enum';
import { StoryChaptersState } from 'src/app/story/state/chapters/story.chapters.state';
import { ModulesThumbnailService } from '../modules-thumbnail.service';
import { ContentType } from 'src/app/story/models/content-type.enum';
import { forkJoin } from 'rxjs';
import { take, finalize } from 'rxjs/operators';
import { SaveModuleModel } from '../models/save-module.model';
import { StoryService } from 'src/app/story/story.service';
import { StoryState } from 'src/app/story/state/story.state';
import { invert } from 'lodash';
import { ChapterUtils } from 'src/app/shared/chapter-utils';
import { ParticleActions } from 'src/app/engine/state/particles/particles.actions';

interface ModulesStateModel {
  primaryCategoryOptions: string[];
  secondaryCategoryOptions: string[];
  moduleExists: boolean;
  saveModule: SaveModuleModel;
  storyType: ContentType;
  saving: boolean;
}

const initialTrim = new TrimConfiguration();
initialTrim.graph.hidden = true;

const initialState = {
  primaryCategoryOptions: [],
  secondaryCategoryOptions: [],
  moduleExists: false,
  saveModule: new SaveModuleModel(),
  storyType: ContentType.Module,
  saving: false
};

@State<ModulesStateModel>({
  name: 'modules',
  defaults: initialState
})
export class ModulesState {
  constructor(
    private store: Store,
    private serializeModuleService: SerializeModuleService,
    private modulesService: ModulesService,
    private modulesThumbnailService: ModulesThumbnailService,
    private storyService: StoryService
  ) { } 

  @Selector()
  static getSaveModule(state: ModulesStateModel) {
    return state.saveModule;
  }

  static getPrimaryCategoryOptions(type: 'read' | 'write') {
    return createSelector(
      [ModulesState],
      (state: ModulesStateModel) => {
        return (type === 'read') ? 
          state.primaryCategoryOptions : 
          state.primaryCategoryOptions.filter(value => value !== 'exports');
      }
    );
  }

  @Selector()
  static getSecondaryCategoryOptions(state: ModulesStateModel) {
    return state.secondaryCategoryOptions;
  }

  @Selector()
  static getSaving(state: ModulesStateModel) {
    return state.saving;
  }


  @Action(SaveModule)
  async saveModule(context: StateContext<ModulesStateModel>, action: SaveModule) {
    context.patchState({ saving: true });

    // Prepare the save configurations
    const saveConfig = action.saveConfig;
    const trimConfig = action.trimConfig ? action.trimConfig : new TrimConfiguration();

    // Generate a new namespace map for the serialize object, included all child properties.
    // Then serialize the module.
    const namespaceMap = saveConfig.duplicate ? action.saveConfig.namespaceMap : {};

    // is save config dicates, generate new chapter Ids.
    const chapters = this.store.selectSnapshot(StoryChaptersState.getStoryChapters);
    const makeNewChapterIds = saveConfig.replace === false;
    const chapterIdMap = ChapterUtils.makeChapterIdMap(chapters, makeNewChapterIds);

    if (makeNewChapterIds) {
      await this.store.dispatch(new ParticleActions.UpdateTweenParticleLookupChapters(chapterIdMap)).toPromise();
    }
    
    // Serialize story
    const serialized = this.serializeModuleService.serialize(
      namespaceMap,
      trimConfig,
      action.saveConfig.duplicate,
      this.store.selectSnapshot(StoryState.getStory),
      this.store.selectSnapshot(StoryChaptersState.getStoryChapters),
      chapterIdMap
    );

    // Save module file to the server.
    this.modulesService.saveModule(serialized.story, action.id)
      .pipe(
        finalize(() => context.patchState({ saving: false }))
      )
      .subscribe(
        (result: ModulesSaveResultEnum) => {
          if (result === ModulesSaveResultEnum.Success) {

            // Save thumbnails
            const thumbs = this.store.selectSnapshot(StoryChaptersState.getRegisteredThumbnails);

            // call service to save
            const indexId = serialized.story.chapters[0].id;
            
            const requests$ = this.modulesThumbnailService.saveStoryThumbnails({
              storyId: action.id,
              storyType: ContentType.Module,
              thumbnails: thumbs,
              chapterIdMap: invert(chapterIdMap), 
              indexId: indexId,
              deleteUnused: saveConfig.replace 
            });
            
            if (requests$.length === 0) {
              context.dispatch(new SaveModuleSuccess(action.id, serialized.story, action.saveConfig.duplicate));  
            } else {
              forkJoin(requests$).subscribe(() => {
                context.dispatch(new SaveModuleSuccess(action.id, serialized.story, action.saveConfig.duplicate));  
              });
            }

          } else {
            context.dispatch(new SaveModuleFailure(action.id));
          }
        }
      );

    // Duplicate module [working]
    if (saveConfig.duplicate) {
      const copyRequests$ = [];
      Object.entries(namespaceMap)
        .forEach(([sourceState, targetState]) => {
          copyRequests$.push(this.modulesService.copyState(sourceState, targetState));
        });

      forkJoin(copyRequests$)
        .subscribe(
          (success) => {
            context.dispatch(new DuplicateModuleSuccess(action.id));
          },
          (error) => {
            context.dispatch(new DuplicateModuleFailure(action.id));
          }
        );
    }
  }

  @Action(SetSaveModule)
  setSaveModule(context: StateContext<ModulesStateModel>, action: SetSaveModule) {
    context.patchState({
      saveModule: {...action.module}
    });
  }

  @Action(GetModulePrimaryCategories)
  getModulePrimaryCategories(context: StateContext<ModulesStateModel>, action: GetModulePrimaryCategories) {
    this.modulesService.getPrimaryCategories()
      .subscribe((categories) => {
        context.patchState({
          primaryCategoryOptions: categories
        });
      });
  }

  @Action(GetModuleSecondaryCategories)
  getModuleSecondaryCategories(context: StateContext<ModulesStateModel>, action: GetModuleSecondaryCategories) {
    this.modulesService.getSecondaryCategories(action.primaryCategory)
      .subscribe((categories) => {
        context.patchState({
          secondaryCategoryOptions: categories
        });
      });
  }

  // # TODO: Wire up full state reset, not just patchState
  @Action(ResetModules)
  resetModules(context: StateContext<ModulesStateModel>, action: ResetModules) {
    forkJoin(this.modulesService.getPrimaryCategories(), this.modulesService.getSecondaryCategories(''))
      .pipe(
        take(1)
      )
      .subscribe(([primaryCategories, secondaryCategories]) => {
        context.patchState({
          primaryCategoryOptions: primaryCategories,
          secondaryCategoryOptions: secondaryCategories
        });
      });
  }
}
