import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  OnDestroy,
} from '@angular/core';
import { MatCheckboxChange } from '@angular/material';
import { BookmarkModel } from 'src/app/bookmarks/models/bookmark.model';
import { AuthState } from 'src/app/auth/state/auth/auth.state';
import { Observable, BehaviorSubject, combineLatest, Subject } from 'rxjs';
import { Select, Store } from '@ngxs/store';
import { takeUntil, filter, take } from 'rxjs/operators';
import { isNil } from 'lodash';
import { GalleryActions } from 'src/app/gallery/state/gallery.actions';
import { GalleryState } from 'src/app/gallery/state/gallery.state';
import {
  BookmarkAction,
  BookmarkActionEvent,
} from 'src/app/bookmarks/models/bookmark-action';
import {
  GalleryCardActions,
  GalleryBulkActions,
} from 'src/app/gallery/state/gallery.state.model';
import { GalleryViewMode } from 'src/app/gallery/models/gallery-view-mode.enum';
import { BookmarksService } from 'src/app/bookmarks/bookmarks.service';
import { BookmarkActionsService } from 'src/app/bookmarks/bookmark-actions.service';
import { BreakpointObserver } from '@angular/cdk/layout';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-gallery-card',
  templateUrl: './gallery-card.component.html',
  styleUrls: ['./gallery-card.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class GalleryCardComponent implements OnInit, OnDestroy {
  application = environment.application;
  bookmarkActionEnum = BookmarkAction;
  galleryViewMode = GalleryViewMode;
  upgradeRequired = false;
  canEdit = false;
  menuOpen = false;

  @Select(AuthState.isPremiumUser)
  isPremiumUser$: Observable<boolean>;

  @Select(AuthState.isPrivateUser)
  isPrivate$: Observable<boolean>;

  @Select(GalleryState.getCheckedResults)
  checkedResults$: Observable<string[]>;

  @Select(GalleryState.getBulkActions)
  bulkActions$: Observable<GalleryBulkActions>;

  @Select(GalleryState.getCardActions)
  cardActions$: Observable<GalleryCardActions>;

  @Select(GalleryState.getViewMode)
  viewMode$: Observable<GalleryViewMode>;

  get bookmarkOtherActions(): BookmarkAction[] {
    return this._bookmarkOtherActions.value;
  }
  set bookmarkOtherActions(value: BookmarkAction[]) {
    this._bookmarkOtherActions.next(value);
  }
  private _bookmarkOtherActions = new BehaviorSubject<BookmarkAction[]>([]);

  get bookmarkAddActions(): BookmarkAction[] {
    return this._bookmarkAddActions.value;
  }
  set bookmarkAddActions(value: BookmarkAction[]) {
    this._bookmarkAddActions.next(value);
  }
  private _bookmarkAddActions = new BehaviorSubject<BookmarkAction[]>([]);

  @Input()
  public get bookmark(): BookmarkModel {
    return this._bookmark.value;
  }
  public set bookmark(value: BookmarkModel) {
    this._bookmark.next(value);
    this.changeDetectorRef.markForCheck();
  }
  private _bookmark = new BehaviorSubject<BookmarkModel>(null);

  @Input()
  selectLabel = 'Select';

  @Input()
  selectLabelPlural = '';

  @Input()
  checkable = false;
  checked = false;

  @Output()
  selectBookmark = new EventEmitter<BookmarkModel>();

  @Output()
  bookmarkAction = new EventEmitter<BookmarkActionEvent>();

  ngUnsubscribe = new Subject();
  defaultImage = '/assets/images/card-thumbnail-placeholder.png';
  label = '';

  constructor(
    private changeDetectorRef: ChangeDetectorRef,
    private store: Store,
    private bookmarksService: BookmarksService,
    private bookmarkActionsService: BookmarkActionsService,
    private breakpiontObserver: BreakpointObserver
  ) {}

  ngOnInit() {
    this.checkPermissions();
    this.checkLabel();
    this.observeChecked();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

  isViewOnly(result: BookmarkModel) {
    return this.bookmarksService.canEdit(result);
  }

  handleSelect(result: BookmarkModel) {
    this.selectBookmark.emit(result);
  }

  handleCheckedChange(event: MatCheckboxChange) {
    this.store.dispatch(
      new GalleryActions.CheckItem(event.source.value, event.checked)
    );
  }

  handleBookmarkAction(cardAction: BookmarkAction, id: string, data: any) {
    this.bookmarkAction.emit({ name: cardAction, id: id, bookmark: data });
  }

  handleMenuOpened() {
    this.menuOpen = true;
  }

  handleMenuClosed() {
    this.menuOpen = false;
  }

  observeChecked() {
    this.checkedResults$
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((results) => {
        this.checked = results.indexOf(this.bookmark.id) > -1;
        this.changeDetectorRef.markForCheck();
      });
  }

  checkPermissions() {
    combineLatest(
      this._bookmark,
      this.isPremiumUser$,
      this.isPrivate$,
      this.breakpiontObserver.observe('(max-width: 959px)')
    )
    .pipe(
      filter(
        ([bookmark, isPremiumUser, isPrivateUser]) =>
          !isNil(bookmark) && !isNil(isPremiumUser) && !isNil(isPrivateUser)
      ),
      takeUntil(this.ngUnsubscribe)
    )
    .subscribe(([bookmark, isPremiumUser, isPrivateUser, breakpoint]) => {
      this.upgradeRequired = !isPremiumUser && bookmark.premium === true;

      // Seperate add actions
      this.bookmarkAddActions = this.bookmarkActionsService.getBookmarkActions(
        bookmark,
        isPremiumUser,
        isPrivateUser,
        {
          includeActions: [
            BookmarkAction.AddToMyModels, 
            BookmarkAction.AddToTeamModels
          ]
        });

      // Never show edit or manage on smaller screens
      this.bookmarkOtherActions = this.bookmarkActionsService.getBookmarkActions(
        bookmark,
        isPremiumUser,
        isPrivateUser,
        {
          excludeActions: [
            BookmarkAction.View,
            BookmarkAction.Edit,
            BookmarkAction.AddToMyModels, 
            BookmarkAction.AddToTeamModels,
            ...(breakpoint.matches) ? [BookmarkAction.Edit, BookmarkAction.Manage] : []
          ]
        }
      );

      // Check if content can be edited
      this.canEdit = this.bookmarkActionsService.canEdit(bookmark, isPremiumUser);

      this.changeDetectorRef.markForCheck();
    });
  }

  checkLabel() {
    this._bookmark
      .pipe(
        takeUntil(this.ngUnsubscribe),
        filter((bookmark) => bookmark != null)
      ).subscribe((bookmark) => {
        this.label = (bookmark.tour && this.selectLabelPlural) ? this.selectLabelPlural : this.selectLabel;
        this.changeDetectorRef.markForCheck();
      });
  }
}
