import { Component, OnInit, EventEmitter, Output, Input, ChangeDetectorRef, ChangeDetectionStrategy } from '@angular/core';
import { SortModel } from 'src/app/bookmarks/models/bookmark-sort.model';

type SortableModel = SortModel<any>;

@Component({
  selector: 'app-sort',
  templateUrl: './sort.component.html',
  styleUrls: ['./sort.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class SortComponent implements OnInit {
  @Input()
  public get sort(): SortableModel {
    return this._sort;
  }
  public set sort(value: SortableModel) {
    this._sort = value;
    this.changeDetectorRef.markForCheck();
  }
  private _sort: SortableModel;

  @Input()
  sortOptions: SortableModel[] = [];

  @Output()
  sortChange = new EventEmitter<SortableModel>();

  constructor(
    private changeDetectorRef: ChangeDetectorRef
  ) { }

  ngOnInit() {
  }

  handleSort(option: SortableModel) {
    this.sortChange.emit(option);
  }

}
