import { LoginBaseModel } from './login-base-model';

// PATH
// /ws/user/sign/up

// SUCCESS
// 200
// null

// ERROR
// 400
// {"error": {"message": "This email address is already in use. <br/>Current users, please sign in.", "code": 400, "arguments": null}}

export interface SignUpResponseModel {
    redirect_url: string;
}

export class SignUpModel extends LoginBaseModel {
    version = 2;

    email: string;
    password: string;
    name: string;
    firstName: string;
    lastName: string;
    
    static toJSON(value: SignUpModel): any {
        return {
            version: value.version,

            email: value.email,
            password: value.password,
            first_name: value.firstName,
            last_name: value.lastName,

            // Required
            group_invite_mode: value.groupInviteMode,
            group_invite_key: value.groupInviteKey,
            group_code_mode: value.groupCodeMode,
            group_code: value.groupCode,

            // optoinal platform params
            platform: value.platform,
            return_to: value.returnTo,
            brand_id: value.brandId,
            locale_id: value.localeId,
            timestamp: value.timestamp
        };
    }
}
