import { Component, OnInit, OnDestroy, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { Store, Select } from '@ngxs/store';
import { GalleryActions } from 'src/app/gallery/state/gallery.actions';
import { MatDialogRef } from '@angular/material';
import { GalleryState } from 'src/app/gallery/state/gallery.state';
import { Observable, Subject } from 'rxjs';
import { GalleryViewMode } from 'src/app/gallery/models/gallery-view-mode.enum';
import { BookmarkModel } from 'src/app/bookmarks/models/bookmark.model';
import { AuthState } from 'src/app/auth/state/auth/auth.state';
import { UserProfileModel } from 'src/app/auth/models/user-profile.model';
import { DialogActions } from '../../state/dialog.actions';
import { GalleryResultsModel } from 'src/app/gallery/models/gallery-results.model';
import { EnvironmentApplication } from 'src/environments/models/environment-application';
import { ContentType } from 'src/app/story/models/content-type.enum';
import { CategorySearchConfig, SearchConfigUnion, LoftSearchConfig } from 'src/app/gallery/models/search-config';
import { SearchOptions } from 'src/app/gallery/models/search-options';
import { bookmarkSortTitleAsc } from 'src/app/bookmarks/data/bookmark-sort';
import { SearchType } from 'src/app/gallery/models/search-type.enum';
import { Content } from '@angular/compiler/src/render3/r3_ast';
import { takeUntil, distinctUntilChanged } from 'rxjs/operators';
import { isEqual } from 'lodash';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-import-model-dialog',
  templateUrl: './import-model-dialog.component.html',
  styleUrls: ['./import-model-dialog.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ImportModelDialogComponent implements OnInit, OnDestroy {
  application = environment.application;
  contentType = ContentType.Module;


  @Select(GalleryState.getViewMode)
  viewMode$: Observable<GalleryViewMode>;

  @Select(GalleryState.getLoading)
  loading$: Observable<boolean>;

  @Select(GalleryState.getFilteredResults)
  results$: Observable<GalleryResultsModel>;

  @Select(GalleryState.getSearchConfig)
  searchConfig$: Observable<SearchConfigUnion>;

  @Select(AuthState.isPremiumUser)
  isPremiumUser$: Observable<boolean>;

  ngUnsubscribe = new Subject();

  constructor(
    private store: Store,
    private dialogRef: MatDialogRef<ImportModelDialogComponent>,
    private changeDetectorRef: ChangeDetectorRef
  ) { }

  ngOnInit() {
    this.initGallery();    
    this.observeSearchConfig();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

  initGallery() {
    const config = this.store.selectSnapshot(GalleryState.getSearchConfig) || 
      environment.application === EnvironmentApplication.Studio ? 
        new CategorySearchConfig('all') : 
        new LoftSearchConfig('production', 'maleAdult');
        
    const options = this.store.selectSnapshot(GalleryState.getSearchOptions) || new SearchOptions('', bookmarkSortTitleAsc);

    this.store.dispatch(new GalleryActions.SetCardActions({ enabled: false }));
    this.store.dispatch(new GalleryActions.SetBulkActions({ enabled: false, items: [] }));
    this.store.dispatch(new GalleryActions.SetSearchConfig(config));
    this.store.dispatch(new GalleryActions.SetSearchOptions(options));
  }

  observeSearchConfig() {
    this.searchConfig$.pipe(
      distinctUntilChanged((a, b) => isEqual(a, b)),
      takeUntil(this.ngUnsubscribe)
    ).subscribe(config => {
      this.contentType = config.type === SearchType.Category ? ContentType.Module : ContentType.Bookmark;
      this.changeDetectorRef.markForCheck();
    });
  }

  handleSelect(bookmark: BookmarkModel) {
    if (!bookmark.premium || this.store.selectSnapshot(AuthState.isPremiumUser)) {
      this.store.dispatch(new GalleryActions.SelectBookmark(bookmark));
      this.dialogRef.close(bookmark);
    } else {
      this.store.dispatch(new DialogActions.OpenPremiumContent());
    }
  }
}
