import { SearchType } from './search-type.enum';

export class CategorySearchConfig {
    public readonly type = SearchType.Category;

    constructor(
        public categoryId?: string,
        public tags: string[] = [''],
        public query: string = '',
    ) {}
}

export class OrganizationSearchConfig {
    public readonly type = SearchType.Organization;

    constructor(
        public organizationId: number,
        public query: string = '',
    ) {}
}

export class CollectionSearchConfig {
    public readonly type = SearchType.Collection;

    constructor(
        public organizationId: number,
        public collectionId: number,
        public query: string = '',
    ) {}
}

export class CommunitySearchConfig {
    public readonly categoryId = 'public';
    public readonly type = SearchType.Community;

    constructor(
        public query: string = ''
    ) { }
}
export class PrivateSearchConfig {
    public readonly type = SearchType.Private;

    constructor(
        public query: string = ''
    ) {}
}

export class InternalSearchConfig {
    public readonly type = SearchType.Internal;

    constructor(
        public query: string = '',
        public directory: string = ''
    ) {}
}

export class LoftSearchConfig {
    public readonly type = SearchType.Loft;

    constructor(
        public primary: string,
        public secondary: string = '',
        public query: string = ''
    ) { }
}


export type SearchConfigUnion = 
    CategorySearchConfig | 
    OrganizationSearchConfig | 
    CollectionSearchConfig | 
    CommunitySearchConfig | 
    PrivateSearchConfig | 
    InternalSearchConfig | 
    LoftSearchConfig;

