import { get } from 'lodash';

// parse server error message
export function parseErrorMessage(error: Error): string {
  const errorMessage: string = get(error, 'error.error.message', '');

  /* NOTE:
    Cleanup server formatted messages (i.e. brackets and single quotes, "['message in brackets and quotes']")
    Should migrate to error codes (number) and define messages client side.
   */
  return errorMessage
    .replace(/^\[(.+)\]$/, '$1')
    .replace(/^\'(.+)\'$/, '$1');
}
